import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { Util } from "../../../shared/utils/util";
import { Email } from "../../../entities/paymentSearchEmail.entity";
import { Storage } from "../../../shared/utils/storage";
import { Payment } from "../../../entities/payment";
import { PaymentFormGroup } from "../../../forms/payment.form";
import { PaymentDataSource } from "./payment.datasource";
import { ApexService } from "../../../shared/service/apex.service";
import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
import { DataSource } from "@angular/cdk";
import { MdSort } from "@angular/material";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";

import { DialogContentExampleDialogPayment } from "./DialogContentExampleDialogPayment";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { PmPaymentDialogContentComponent } from "./pm.payment-dialog-content.component";

@Component({
  selector: "app-payment-search",
  templateUrl: "./payment.search.component.html",
  styleUrls: ["./payment.search.component.scss"]
})
// implements OnInit
export class PaymentSearchComponent {
  dataSource: PaymentDataSource = null;
  selectedItem: any;
  showSide: boolean = false;
  selectedItemForView: any = null;
  serachObj: any = {};
  email: any = {};
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;
  displayedColumns: any[] = [
    "Invoice_Number",
    "companyName",
    "Payment_Date",
    "Payment_Mode",
    "amount",
    "Action"
  ];
  //  dataSource : any = null;
  constructor(
    private appService: AppService,
    private restService: RestService,
    private apexService: ApexService,
    private _dataTableService: TdDataTableService,
    public dialog: MdDialog
  ) {}
  //
  ngOnInit(): void {
    //this.serachObj.period = "year";
    this.dataSource = new PaymentDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
    //        this.customerLoad();
    this.payModeLoad();
    this.periodLoad();
    this.rangeLoad();
  }

  payModeList: any[] = [];
  payModeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.payModeList = results["PayModeSearch"];
        }
      });
  }

  periodList: any[] = [];
  periodLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.periodList = results["Period"];
      });
  }
  rangeList: any[] = [];
  rangeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.rangeList = results["Range"];
      });
  }

  //    customerList: any[] = [];
  //    customerLoad() {
  //
  //        this.restService.retriveCustomerAllforCMB()
  //            .subscribe(results => {
  //                if (results) {
  //                    this.customerList = results;
  //
  //                }
  //            })
  //    }
  clearAll() {
    this.serachObj = {};
    this.dataSource = new PaymentDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
  }

  // dataList: any = []

  edit() {
    console.log(
      "edit function called" + this.selectedItem.customer.businessName
    );
    Storage.setLocalItem("selectedItem", this.selectedItem);
    this.appService.navigate("./admin/payment", [
      { id: this.selectedItem.id, isEdit: "true" }
    ]);
  }
  pop() {
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }
  opens(id) {
    // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
    let companyInfo = Storage.getSessionUser();

    let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
    if (parseInt(pageLayoutId) == 1) {
      const dialogRef = this.dialog.open(PmPaymentDialogContentComponent, {
        width: "600px",
        data: id
      });
    }
  }

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogContentExampleDialogPayment, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  delete() {
    this.openDialog({ id: [this.selectedItem.id] });
    // this.restService.paymentDelete({ id: [this.selectedItem.id] })
    //     .subscribe(result => {

    //         this.apexService.showMessages("payment receipt deleted successfully.", 1500);
    //         this.dataSource = new PaymentDataSource(this.restService, this.searchObject, this.sort);
    //     });
  }
  //
  onSelectedItem(row) {
    this.selectedItem = row;
  }
  // serachObj: any = {};
  //
  callDataSource() {
    if (this.serachObj.fromDate && typeof this.serachObj.fromDate == "object") {
      this.serachObj.fromDate = Util.ISODate(
        this.serachObj.fromDate.toISOString()
      );
    }
    if (this.serachObj.toDate && typeof this.serachObj.toDate == "object") {
      this.serachObj.toDate = Util.ISODate(this.serachObj.toDate.toISOString());
    }
    if (
      this.serachObj.paymentDate &&
      typeof this.serachObj.paymentDate == "object"
    ) {
      this.serachObj.paymentDate = Util.ISODate(
        this.serachObj.paymentDate.toISOString()
      );
    }

    this.dataSource = new PaymentDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
  }
  // pop() {
  //   Storage.setLocalItem("selectedItem", this.selectedItem);
  //   setTimeout(() => {
  //     this.opens(null);
  //   }, 10);
  // }
  // opens(id) {
  //   // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
  //   let companyInfo = Storage.getSessionUser();

  //   let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
  //   if (parseInt(pageLayoutId) == 1) {
  //     const dialogRef = this.dialog.open(PmPaymentDialogContentComponent, {
  //       width: "600px",
  //       data: id
  //     });
  //   }
  // }
  view() {
    this.appService.navigate("./admin/paymentView", [
      { id: this.selectedItem.id, isEdit: "false" }
    ]);
  }
  download() {
    this.restService.paymentDownload(this.selectedItem.id);
  }

  sendEmail() {
    this.showSide = true;
    // if (!item) {
    let item = new Email();
    // }
    this.email = Object.assign({}, item);
    this.email.to = this.selectedItem.emailId;
  }
  onClose(item: any) {
    this.showSide = false;
  }
  emailSave() {
    this.email.from = this.appService.getSessionUser().email;
    this.email.fileName = "Payment.pdf";
    this.email.content = btoa(this.email.message);
    delete this.email.message;

    this.restService
      .paymentEmail(this.selectedItem.id, this.email)
      .subscribe(result => {
        this.showSide = false;
      });
  }

  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/paymentView", [
      { id: this.selectedItemForView.id }
    ]);
  }
}



