import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { Storage } from "../../../shared/utils/storage";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { ExpenseFormGroup } from "../../../forms/expense.form";
import { ExpenseManagement } from "../../../entities/expensemanagement";
import { SupplierManagement } from "../../../entities/suppliermanagement";

import { ExpenseAccountDetails } from "../../../entities/expenseaccountdetails.entity";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/operator/map";
import { FormControl, Validators } from "@angular/forms";
import { ExpensePayee } from "../../../../app/entities/expensepayee.entity";
// import { SSL_OP_ALL } from 'constants';

@Component({
  selector: "app-expensemanagement",
  templateUrl: "./expensemanagement.component.html",
  styleUrls: ["./expensemanagement.component.scss"]
})
export class ExpensemanagementComponent implements OnInit {
  myForm: any = ExpenseFormGroup.init();
  expense: any;
  controls: any;
  // email = new FormControl('', [Validators.required, Validators.email]);

  supplierManagement: SupplierManagement;
  firstName: string = null;
  middleName: string = null;
  lastName: string = null;
  suppliers: SupplierManagement[] = [];
  expenseManagement: ExpenseManagement;
  showElectric = false;
  showCheque = false;
  isRecordFound = false;
  isSupplierToggle = true;
  public showControls: boolean = false;
  public showMsg: boolean = false;
  stateList: any[] = [];
  filteredStates: any[] = [];
  filteredBanks: any[] = [];

  sales: any;
  isEditable: string = "true";
  isEdit: boolean = false;
  public isvalid: boolean;
  public isInvalid: boolean;
  minDate: Date;

  newExpense() {
    return {};
  }
  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    ExpenseFormGroup.newPayee(this.myForm);
    ExpenseFormGroup.edit(this.myForm);

    this.expenseManagement = new ExpenseManagement();

    this.supplierManagement = new SupplierManagement();

    this.expense = this.newExpense;
    this.expenseManagement.isSupplier = true;
    this.expenseManagement.payeeId.id = null;
    this.minDate = this.expenseManagement.billDate = new Date();
    this.minDate.setDate(this.minDate.getDate());
  }

  ngOnInit() {
    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.expenseDetails(id);
      this.retriveSuppliers(id);
      this.isEdit = true;
    } else {
      this.isEdit = false;
      this.isEditable = "true";
    }

    this.payModeLoad();
    this.addItem();
    this.clearItem();
    this.bankLoad();
    this.stateLoad();
    this.payeeLoad();
    this.retreiveExpenseType();
    this.minDate = new Date();
  }

  stateLoad() {
    this.restService
      .dataloadFromFile("./assets/json/states.json")
      .subscribe(results => {
        this.stateList = results["states"];
      });
  }
  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.key.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }

  // Check Payment Date should be greater than Bill Date
  // compareDate() {
  //   let billDate = this.expenseManagement.billDate;
  //   let payDate = this.expenseManagement.paymentDate;
  //   if (payDate < billDate) {
  //     this.showControls = true;
  //   } else {
  //     this.showControls = false;
  //   }
  // }
  // Check Payment Date should be greater than cheque Date
  comparePaymentDate() {
    let payDate = this.expenseManagement.paymentDate;
    let chequeDate = this.expenseManagement.chequeDate;
    //if(chequeDate !== null){
    if (chequeDate < payDate) {
      this.showMsg = true;
    } else {
      this.showMsg = false;
    }
  }

  onSupplierToggleChange($event) {
    if ($event.checked) {
      this.expenseManagement.expenseName = null;
      this.expenseManagement.payeeId.id = null;
    } else {
      ExpenseFormGroup.newPayee(this.myForm);
    }
  }

  onPayeeToggleChange($event) {
    if ($event.checked) {
      ExpenseFormGroup.newPayee(this.myForm);
    }
  }

  onPayeeChange($event) {
    console.log($event);
  }

  expenseDetails(id) {
    this.restService.expenseEdit(id).subscribe(result => {
      console.log("Response:" + JSON.stringify(result));
      if (result) {
        this.expenseManagement = result;
        this.expenseManagement.creditAccountId = result["creditAccountId"];
        this.expenseManagement.referenceNo = result["referenceNo"];

        this.isSupplierToggle = this.expenseManagement.isSupplier;
        if (this.isSupplierToggle) {
          ExpenseFormGroup.supplierName(this.myForm);
          this.isSupplierToggle = true;
          this.expenseManagement.isSupplier = true;
          this.expenseManagement.expenseName = null;
          this.expenseManagement.payeeId = new ExpensePayee();
          this.expenseManagement.payeeId.id = null;
        } else {
          ExpenseFormGroup.newPayee(this.myForm);
        }

        this.isRecordFound = true;
        this.onPayModeChange(this.expenseManagement.paymentMode);
        this.totalCalculate();
      }
    });
  }

  totalCalculate() {
    let subTotal: number = 0;
    let totalTax: number = 0;

    this.expenseManagement.expenseAccountDetails.forEach(item => {
      subTotal += item.amount;
      totalTax += item.tax;

      console.log("sub total", subTotal);
      console.log("total tax", totalTax);
    });

    this.expenseManagement.subTotal = parseFloat(subTotal.toString());
    this.expenseManagement.totalTax = parseFloat(totalTax.toString());
    this.expenseManagement.grandTotal =
      this.expenseManagement.subTotal + this.expenseManagement.totalTax;
    this.expenseManagement.amount = this.expenseManagement.grandTotal;
  }

  taxInfo() {
    // if(this.expenseManagement.expenseName == "" || this.expenseManagement.expenseName == null){
    //   let item = new ExpenseAccountDetails();
    //   item.tax = 0;
    // }
    // let item = new ExpenseAccountDetails();
    //    if(item.amount = 0){
    //       item.tax = 0;
    //     }
  }

  itemValueChange(item: any, field: string) {
    console.log("field is ", field);
    //  this.taxInfo();
    this.itemCalculate(item);
    this.totalCalculate();
    if (field == "r") {
      this.addItem();
    }
  }
  itemCalculate(item: any) {
    item.itemTotal = item.amount * item.tax;
  }
  addItem() {
    if (
      this.expenseManagement.expenseAccountDetails.length == 0 ||
      this.expenseManagement.expenseAccountDetails[
        this.expenseManagement.expenseAccountDetails.length - 1
      ].expenseTypes != null
    )
      console.log("inside addItem");
    let item = new ExpenseAccountDetails();
    item.id = null;
    item.description = item.description;
    item.tax = 0;
    this.expenseManagement.expenseAccountDetails.push(item);
  }

  clearItem() {
    this.expenseManagement = new ExpenseManagement();
    this.myForm.reset();
    setTimeout(() => {
      this.addItem();
    }, 10);
  }

  // newExpense() {
  //   return {};
  // }
  //mycode

  onPayModeChange(val: string) {
    if (val == "Cheque") {
      ExpenseFormGroup.cheque(this.myForm);

      this.showCheque = true;
      this.showElectric = false;
      this.expenseManagement.referenceNo = null;
    } else if (val == "Electronic") {
      ExpenseFormGroup.electric(this.myForm);

      this.showElectric = true;
      this.showCheque = false;
      this.expenseManagement.bankName = null;
      this.expenseManagement.chequeNo = null;
      this.expenseManagement.chequeDate = null;
    } else {
      ExpenseFormGroup.cash(this.myForm);
      this.showCheque = false;
      this.showElectric = false;
      this.expenseManagement.bankName = null;
      this.expenseManagement.chequeNo = null;
      this.expenseManagement.chequeDate = null;
      this.expenseManagement.referenceNo = null;
    }
  }

  save() {
    console.log("Request: " + JSON.stringify(this.expenseManagement));
    let filteredItems: ExpenseAccountDetails[] = this.expenseManagement.expenseAccountDetails.filter(
      s => s.expenseTypes.id != "undefined" && s.expenseTypes.id != null
    );
    this.expenseManagement.expenseAccountDetails = filteredItems;
    this.restService.expenseSave(this.expenseManagement).subscribe(result => {
      let message = "-----------------";
      if (this.isEdit) {
        message = "Expense updated successfully.";
        //this.expenseManagement = result;
      } else {
        message = "Expense saved successfully.";
      }

      this.appService.showMessage(message);

      if (this.isRecordFound) {
        this.appService.navigate("./admin/expense-search", []);
      } else {
        this.appService.navigate("./admin/expense-search", []);
        //this.appService.navigate('./admin/expense-managementView', [{ id: this.expenseManagement.id }]);
      }
    });
  }

  retriveSuppliers(id: string) {
    this.restService.retriveSupplier().subscribe(result => {
      if (result) {
        this.suppliers = result;
        this.isEdit = true;
        this.suppliers.forEach(item => {
          item.displayName = item.companyName;
        });
      }
    });
  }
  expenseTypes: any = [];
  retreiveExpenseType() {
    this.restService.getExpenseTypes().subscribe(res => {
      if (res) {
        this.expenseTypes = res;
        this.sortBy("expenseName");
      }
    });
  }
  sortBy(field: string) {
    this.expenseTypes.sort((a: any, b: any) => {
      if (a[field] < b[field]) {
        return -1;
      } else if (a[field] > b[field]) {
        return 1;
      } else {
        return 0;
      }
    });
    this.expenseTypes = this.expenseTypes;
  }
  payModeList: any[] = [];
  payModeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.payModeList = results.PayMode;
        }
      });
  }

  bankList: any[] = [];
  bankLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.bankList = results.Bank;
      });
  }

  filterBanks(val: string) {
    console.log(val);
    this.filteredBanks =
      val && val != ""
        ? this.bankList.filter(
            s => s.key.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.bankList;
  }

  taxList: any[] = [];
  taxLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.taxList = results.Tax;
        }
      });
  }
  // expenseTypeList: any[] = [];
  // expenseTypeLoad() {
  //   this.restService.dataload("ExpenseType").subscribe(results => {
  //     if (results) {
  //       this.expenseTypeList = results;
  //     }
  //   });
  //   this.addItem();
  // }

  generalExpenseList: any[] = [];
  generalExpenseLoad() {
    this.restService.dataload("GeneralExpense").subscribe(results => {
      if (results) {
        this.generalExpenseList = results;
      }
    });
  }

  getDataOnSuccess(response) {
    this.expenseManagement.expenseAccountDetails = response.Items;
  }

  removeItem(index: number) {
    // if (index !== -1) {
    //   // this.sales.items.splice(index, 1);
    //   // this.expense.expenseDetails.splice(index, 1);
    //   this.expenseManagement.expenseAccountDetails.splice(index, 1);
    //   this.totalCalculate();
    // }
    this.expenseManagement.expenseAccountDetails[index].expenseTypes = null;
    if (this.expenseManagement.expenseAccountDetails.length > 0) {
      setTimeout(() => {
        this.expenseManagement.expenseAccountDetails.splice(index, 1);
        this.totalCalculate();
      }, 10);
    }
  }

  printPriview() {
    this.restService.invoiceOpen(this.sales.id);
  }
  payeeList: any[] = [];
  payeeListTemp: any[] = [];

  payeeLoad() {
    this.restService.getPayees().subscribe(results => {
      this.payeeList = results;
      console.log(this.payeeList);
      this.payeeListTemp = results;
    });
  }

  filteredpayees: any[] = [];

  filterpayees(val: string) {
    console.log("value selected : ", val);
    console.log(this.payeeList);

    this.filteredpayees =
      val && val != ""
        ? this.payeeList.filter(
            s =>
              s.name.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
              s.gst_reg_number.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.payeeList;
  }
  gstnumber: any;
  payeeId: any;

  selectedPayee(payee: any) {
    this.expenseManagement.payeeId.id = payee.id;
    // if ()
    this.expenseManagement.payeeId.gst_reg_number = payee.gst_reg_number;
    this.expenseManagement.payeeId.address = payee.address;
    this.expenseManagement.payeeId.state = payee.state;
  }

  compareByOptionId(payeeId, gstnumber) {
    return payeeId && gstnumber && payeeId.id == gstnumber.id;
  }

  isReadOnlyC(item, isEditable) {
    if (isEditable == "true") {
      console.log("isEditable is ", isEditable);
      console.log("Expense is  ", item.expenseTypes.id);
      console.log("expense ", item.amount);
      if (item.expenseTypes.id == null) {
        console.log("Expense is  ", isEditable);
        return true;
      } else {
        return false;
      }
    } else {
      console.log("isEditable is ", isEditable);
      return true;
    }
  }

  isReadOnlyAmount(item, isEditable) {
    if (isEditable == "true") {
      console.log("isEditable is ", isEditable);
      console.log("Expense is  ", item.expenseTypes.id);
      console.log("expense ", item.amount);
      if (item.amount <= 0) {
        return true;
      }
      if (item.expenseTypes.id == null) {
        console.log("Expense is  ", isEditable);
        return true;
      } else {
        return false;
      }
    } else {
      console.log("isEditable is ", isEditable);
      return true;
    }
  }

  filteredExpenseTypes: any[] = [];
  filterExpenseType(val: string) {
    this.filteredExpenseTypes =
      val && val != ""
        ? this.expenseTypes.filter(
            s => s.expenseName.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.expenseTypes;
  }
  isValidPurchaseOrder() {
    const first = this.expenseManagement.expenseAccountDetails[0];
    if (first && first.expenseTypes) {
      return first.amount > 0;
    }
    return false;
  }
}



