import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { InvoiceFormGroup } from "../../../forms/invoice.form";
import { CustomManagement } from "../../../entities/custommanagement";
import { CustommanagementFormGroup } from "../../../forms/custommanagement.form";
import { CustomerDataSource } from "./customer.datasource";
import { DataSource } from "@angular/cdk";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import { DialogContentExampleDialogCustomer } from "./DialogContentExampleDialogCustomer";
import {
  MdDialog,
  MdSort,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { Util } from "../../../shared/utils/util";

@Component({
  selector: "app-customer-search",
  templateUrl: "./customer.search.component.html"
})
export class CustomerSearchComponent {
  dataSource: CustomerDataSource = null;
  displayedColumns: any[] = ["name", "company", "mobile", "state", "action"];
  selectedItem: any = null;
  selectedItemForView: any = null;
  @ViewChild("filter") filter: ElementRef;
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;
  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {}
  searchObj: any = {};

  ngOnInit() {
    this.dataSource = new CustomerDataSource(
      this.restService,
      this.searchObj,
      this.sort,
      this.paginator
    );
    Observable.fromEvent(this.filter.nativeElement, "keyup")
      .debounceTime(100)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) {
          return;
        }
        this.dataSource.filter = this.filter.nativeElement.value;
      });
  }
  callDataSource() {
    if (this.searchObj.fromDate && typeof this.searchObj.fromDate == "object") {
      this.searchObj.fromDate = Util.ISODate(
        this.searchObj.fromDate.toISOString()
      );
    }
    if (this.searchObj.toDate && typeof this.searchObj.toDate == "object") {
      this.searchObj.toDate = Util.ISODate(this.searchObj.toDate.toISOString());
    }
    this.dataSource = new CustomerDataSource(
      this.restService,
      this.searchObj,
      this.sort,
      this.paginator
    );
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    this.searchObject = {};
    this.dataSource = new CustomerDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
  }

  //onKeydown(filterValue: string) {
  // if (filterValue.length < 3) return;
  // if (filterValue.length < 4) {
  //   // For First three letters no search - display all
  //   this.clearAll();
  // } else this.ApplyFilter(filterValue);
  //}
  // public ApplyFilter(filterValue: string) {
  //   this.searchObject = {};
  //   this.dataSource = new CustomerDataSource(
  //     this.restService,
  //     {},
  //     this.sort,
  //     this.paginator
  //   );
  //   filterValue = filterValue.trim().toLowerCase();
  //   this.dataSource.filter = filterValue;
  // }
  searchObject: any = {};
  dataList: any = [];
  edit() {
    this.appService.navigate("./admin/customer-edit", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  view() {
    this.appService.navigate("./admin/customer-details", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogContentExampleDialogCustomer, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  public clearAll() {
    this.searchObject = {};
    this.dataSource = new CustomerDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
  }

  delete() {
    this.openDialog({ id: [this.selectedItem.id] });
  }

  onSelectedItem(row) {
    this.selectedItem = row;
  }

  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/customer-details", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);
  }
}



