import { Component, OnInit, Input } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from '../../../common/rest.service';
import { Prop } from '../../../common/props';
import { Util } from '../../../shared/utils/util';

import { InventoryFormGroup } from '../../../forms/Product.form';

import { Inventory } from '../../../entities/Product.entity'; 
import { ProductPurchasing } from '../../../entities/ProductPurchasing.entity'; 
import { ProductPriceListItems } from '../../../entities/ProductPriceListItems.entity'; 

@Component({
    selector: 'app-noninventory',
    templateUrl: './noninventory.edit.component.html'
})
export class NonInventoryEditComponent {
    myForm: any = InventoryFormGroup.init();
    inventory: Inventory = new Inventory();
    @Input() id; 
    purchaseTaxList: any[] = []; 
    taxList: any[] = []; 
    expenseAccountList: any[] = []; 
    revenueAccountList: any[] = []; 
    invAssetAccountList: any[] = []; 
    public isEditable = false;

    constructor(private appService: AppService, private restService: RestService) {
        InventoryFormGroup.edit( this.myForm );
        let id = this.appService.getParam("id");
        this.isEditable = this.appService.getParam("isEditable");
        if (id !== undefined) { 
            this.retrieve(id);
        }
    }
    ngOnInit() {
        this.init();
    }
    private init() {
         this.inventory.productType = 'NonInventory' ;
        // let id = this.id;
        // if( id ) {
        //     this.retrieve(id);
        // } 
        this.purchaseTaxLoad(); 
        this.taxLoad(); 
        this.expenseAccountLoad(); 
        this.revenueAccountLoad(); 
        this.invAssetAccountLoad(); 
    }
    
    private purchaseTaxLoad() {
        this.restService.dataload('NewPurchaseTax')
            .subscribe(results => {
                if (results) {
                    this.purchaseTaxList = results;
                }
            });
    }
    
    private taxLoad() {
        this.restService.dataload('NewTax')
            .subscribe(results => {
                if (results) {
                    this.taxList = results;
                }
            });
    }
    
    private expenseAccountLoad() {
        this.restService.dataload( '/expenseAccount' )
            .subscribe(results => {
                if (results) {
                    this.expenseAccountList = results;
                }
            });
    }
    
    private revenueAccountLoad() {
        this.restService.dataload( '/revenueAccount' )
            .subscribe(results => {
                if (results) {
                    this.revenueAccountList = results;
                }
            });
    }
    
    private invAssetAccountLoad() {
        this.restService.dataload( '/AssetAccount' )
            .subscribe(results => {
                if (results) {
                    this.invAssetAccountList = results;
                }
            });
    }
    

    retrieve(id: string) {
        this.restService.inventoryEntity(id)
            .subscribe(result => {
                if (result) {
                    this.inventory= result;
                    
                }
            });
    }

    valid() {
        return true;
    }

    save() {
        if (!this.valid()) return;
        this.inventory.productType = 'NonInventory' ;
        this.inventory.companyId = this.appService.getBranch().id;
        this.inventory.active = true;
        this.restService.inventorySave(this.inventory)
            .subscribe(result => {
                if (result) {
                    //console.log("result ======"+result);
                    this.inventory.id= result.id;
                    this.appService.showMessage('Saved successfully...!');
                    this.myForm.reset();
                    //event.preventDefault();
                    //this.appService.navigate('admin/product-search',[]);
                    //location.reload();
                    
                    
                }
            });
    }

    add() {
        this.inventory = new Inventory();
        this.myForm = InventoryFormGroup.init();
    }

    back() {
        this.appService.navigate('', []);
    }

}


