import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { SupplierAddress } from "./../../../entities/supplieraddress.entity";
import { SupplierFormGroup } from "../../../forms/supplier.form";
import { SupplierManagement } from "../../../entities/suppliermanagement";
import { Util } from "../../../shared/utils/util";

@Component({
  selector: "app-business",
  templateUrl: "./sm.bussiness.component.html"
})
export class SmBusinessComponent implements OnInit {
  // address:Address;
  myForm: any = SupplierFormGroup.init();
  isSameBillingAddress: boolean = true;
  public isEditable : boolean = false;
  @Input("supplierManagement") supplierManagement: SupplierManagement;
  //@Output('addressesEvent') addressesEvent = new EventEmitter<string>();
  @Output("supplierBussinessDetail") supplierBussinessDetail = new EventEmitter<
    string
  >();
  @Output("backEvent") backEvent = new EventEmitter<string>();

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    SupplierFormGroup.bankAccountDetails(this.myForm);
    this.isEditable = this.appService.getParam("isEditable");
    //        let data = this.appService.getLocalItem('supplier');
    //        console.log(data);
  }

  ngOnInit() {
    console.log("Account Address");
    //        this.stateLoad();
  }

  next() {
    this.supplierBussinessDetail.emit();
  }
  back() {
    this.backEvent.emit();
  }

  //  addressChange() {
  //      if(this.isSameBillingAddress) {
  //        SupplierFormGroup.shippingAddress(this.myForm, false);
  //      } else {
  //        SupplierFormGroup.shippingAddress(this.myForm, true);  //      }
  //  }

  // cityList: any[] = [];
  // cityLoad() {
  //   this.restService.dataload('City')
  //     .subscribe( results => {
  //       this.cityList = results;
  //     })
  // }

  stateList: any[] = [];
  stateLoad() {
    this.restService.dataload("State").subscribe(results => {
      this.stateList = results;
    });
  }
}



