import { Component, OnInit } from '@angular/core';
import { DataSource} from '@angular/cdk';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from '../../../common/rest.service';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { MdSort, MdPaginator } from '@angular/material';


export class TranscationDataSource extends DataSource<any> {
   _filterChange = new BehaviorSubject('');

  public get filter(): string { return this._filterChange.value; }
  public set filter(filter: string) { this._filterChange.next(filter); }

public data: any[] = [];
  constructor(private restService: RestService, private searchObj: any, private sort: MdSort, private paginator: MdPaginator) {
    super();
    this.restService.searchInvoice(this.searchObj).subscribe(results => {
      if (results) {
        // this.data = [];
        // results.forEach(element => {
        //   if(this.data.length < 10){
        //     this.data.push(element);
        //   }
        this.data = results;
          
        }});
        
      }
 

  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): any {
   const displayDataChanges = [
     this.restService.searchInvoice(this.searchObj),
      this._filterChange,
      this.sort.mdSortChange,
      this.paginator.page,
    ];
    return Observable.merge(...displayDataChanges).map(() => {
      return this.data.slice().filter((item: any) => {
        if(item) {
            let searchStr: string = (item.invoiceNumber +""+ item.saveType +""+ item.invoiceDate);
            return searchStr.toLowerCase().indexOf(this.filter.toLowerCase()) != -1;
        } else {
          return true;
        }

      });
    });  
   // return Promise.resolve([])
  }

  disconnect() {}
}


