import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";

export class StockItemFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }

  static productGroup(group: FormGroup) {
    group.addControl(
      "si_category",
      this.getController(false, this.TYPE_DATA, null, 100)
    );
    group.addControl(
      "si_company",
      this.getController(false, this.TYPE_DATA, null, 100)
    );
    group.addControl(
      "si_brand",
      this.getController(false, this.TYPE_DATA, null, 100)
    );
    group.addControl(
      "si_manufacturer",
      this.getController(false, this.TYPE_DATA, null, 100)
    );
    group.addControl(
      "batch_no",
      this.getController(false, this.TYPE_DATA, null, 100)
    );
  }

  static detailsGroup(group: FormGroup) {
    group.addControl(
      "si_itemname",
      this.getController(true, this.TYPE_DATA, null, 50)
    );
    group.addControl(
      "si_uqc",
      this.getController(null, this.TYPE_DATA, null, 50)
    );
    group.addControl(
      "si_sku",
      this.getController(null, this.TYPE_DATA, null, 30)
    );
    group.addControl(
      "hsn",
      this.getController(null, this.HSN_PATTERN, null, 8)
    );

    group.addControl(
      "si_barcode",
      this.getController(null, this.TYPE_NUMBER, null, 50)
    );
  }

  static pricingGroup(group: FormGroup) {
    group.addControl(
      "si_quantity",
      this.getController(true, this.TYPE_NUMBER, null, 50)
    );
    group.addControl("tax", this.getController(true, this.TYPE_DATA, null, 50));
    group.addControl(
      "si_mrp",
      this.getController(true, this.TYPE_DECIMAL, null, 50)
    );
    group.addControl(
      "si_mrp_datefrom",
      this.getController(null, this.TYPE_DATE, null, null)
    );
    group.addControl(
      "si_mrp_dateto",
      this.getController(null, this.TYPE_DATE, null, null)
    );

    group.addControl(
      "si_cost",
      this.getController(null, this.TYPE_DECIMAL, null, 50)
    );
    group.addControl(
      "si_cost_datefrom",
      this.getController(null, this.TYPE_DATE, null, null)
    );
    group.addControl(
      "si_cost_dateto",
      this.getController(null, this.TYPE_DATE, null, null)
    );

    group.addControl(
      "si_sellingprice",
      this.getController(true, this.TYPE_DECIMAL, null, 50)
    );
    group.addControl(
      "si_sellingprice_datefrom",
      this.getController(null, this.TYPE_DATE, null, null)
    );
    group.addControl(
      "si_sellingprice_dateto",
      this.getController(null, this.TYPE_DATE, null, null)
    );
    group.addControl(
      "supplier",
      this.getController(true, this.TYPE_DATA, null, null)
    );
  }

  static removeDetails(group: FormGroup) {
    group.removeControl("si_uqc");
    group.removeControl("si_sku");
    group.removeControl("hsn");

    group.removeControl("si_barcode");
  }

  static removePricing(group: FormGroup) {
    group.removeControl("si_quantity");

    group.removeControl("si_mrp");
    group.removeControl("si_mrp_datefrom");
    group.removeControl("si_mrp_dateto");

    group.removeControl("si_cost");
    group.removeControl("si_cost_datefrom");
    group.removeControl("si_cost_dateto");

    group.removeControl("si_sellingprice");
    group.removeControl("si_sellingprice_datefrom");
    group.removeControl("si_sellingprice_dateto");
  }
}



