import { Component, OnInit, Output, EventEmitter, Input } from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { ReportFilter } from "app/entities/report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
declare let google: any;

@Component({
  selector: "app-receivables-chart",
  templateUrl: "./receivables-chart.component.html",
  styleUrls: ["./receivables-chart.component.scss"]
})
export class ReceivablesChartComponent implements OnInit {
  reportFilter: WebReportFilter = new WebReportFilter();
  x: any;
  data: any;
  @Input()
  monthsList: any[];
  receivableCurrent: any;
  receivableOverdue: any;
  totalReceivables: any;
  overduePercent: any;
  currentPercent: any;

  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    google.charts.load("current", { packages: ["corechart", "bar"] });
    google.charts.setOnLoadCallback(this.drawAxisTickColors);
    this.receivableLoad();
  }

  ngOnInit() {
    this.x = setInterval(() => {
      this.drawAxisTickColors();
    }, 1000);
    // this.pauseTimeLine(this.x);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }
  drawAxisTickColors = () => {
    var date = new Date();
    var prevYear = date.getFullYear() - 1;
    var year =
      prevYear +
      "-" +
      date
        .getFullYear()
        .toString()
        .substring(2);
    var data = google.visualization.arrayToDataTable([
      ["Genre", "Current", "Over Due", { role: "annotation" }],
      [year, this.currentPercent, this.overduePercent, ""]
    ]);

    var options = {
      colors: ["#009688", "#ffc107"],

      height: 40,
      legend: { position: "top" },
      isStacked: true
    };
    var chart = new google.visualization.BarChart(
      document.getElementById("chart")
    );
    chart.draw(data, options);
  };

  onSelectMonth(item) {
    this.reportFilter.month = item.key;
    this.receivableLoad();
  }
  receivableLoad() {
    this.reportFilter.report = "report=DB_RECEIVABLES";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        // console.log(results);
        this.receivableCurrent = results[0].TotalCurrent;
        this.receivableOverdue = results[0].TotalOverDue;
        this.totalReceivables = results[0].TotalReceivables;
        this.currentPercent =
          (this.receivableCurrent / this.totalReceivables) * 100;
        this.overduePercent = 100 - this.currentPercent;

        if (
          this.receivableCurrent == null &&
          this.receivableOverdue == null &&
          this.totalReceivables == null
        ) {
          this.receivableCurrent = 0;
          this.receivableOverdue = 0;
          this.totalReceivables = 0;
          this.currentPercent = 0;
          this.overduePercent = 0;
        }
        // console.log(this.overduePercent);

        this.drawAxisTickColors();
      });
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



