import { AppService } from "./../../../../shared/service/app.service";
import { Component, OnInit, Input, Output } from "@angular/core";

import { CRDN } from "./../../../../entities/crdn.entity";
import { Service } from "./../../../../entities/Service.entity";
import { ProductComponent } from "./../../../product/product.component";
import { CRDNFormGroup } from "../../../../forms/crdn.form";
import { RestService } from "../../../../common/rest.service";
import { StockItem } from "../../../../entities/stockitem.entity";
import { Util } from "app/shared/utils/util";
import { Customer } from "app/entities/customer.entity";
