import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { Util } from "../../../shared/utils/util";
import { ExpenseManagement } from "../../../entities/expensemanagement";
import { SupplierManagement } from "../../../entities/suppliermanagement";
import { ExpenseFormGroup } from "../../../forms/expense.form";
import { ExpenseDataSource } from "./expense.datasource";
import { ApexService } from "../../../shared/service/apex.service";
import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
import { DataSource } from "@angular/cdk";
import { MdSort } from "@angular/material";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import { Email } from "../../../entities/expenseSearchEmail.entity";
import { DialogContentExampleDialogExpense } from "./DialogContentExampleDialogExpense";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";

@Component({
  selector: "app-expense-search",
  templateUrl: "./expense.search.component.html",
  styleUrls: ["./expense.search.component.scss"]
})
export class ExpenseSearchComponent implements OnInit {
  // dataSource: any
  dataSource: ExpenseDataSource = null;
  supplierManagement: SupplierManagement;
  suppliers: SupplierManagement[] = [];
  //dataSource: SupplierDataSource = null
  selectedItemForView: any = null;
  displayedColumns: any[] = [
    "companyName",
    "billDate",
    "amount",
    "paymentMode",
    "action"
  ];
  selectedItem: any = null;
  showSide: boolean = false;
  searchObject: any = {};
  email: any = {};
  min: Date;
  max: Date;
  //@ViewChild('filter') filter: ElementRef;
  //searchObj: any = {};
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;

  constructor(
    private appService: AppService,
    private restService: RestService,
    private apexService: ApexService,
    private _dataTableService: TdDataTableService,
    public dialog: MdDialog
  ) {
    this.supplierManagement = new SupplierManagement();
    this.retriveSuppliers();
  }

  ngOnInit(): void {
    // this.dataSource = this.restService.dataloadFromFile('./assets/json/allexpenses.json')
    // console.log('hhjhjgjgj'+this.dataSource);
    this.dataSource = new ExpenseDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );

    //    $(el.nativeElement).on('click', function () {
    //            $(this).confirm({
    //                confirm: function () {
    //                    return true;
    //                }
    //            });
    //
    //            return false;
    //        });
    this.payModeLoad();
    this.rangeLoad();
    this.periodLoad();
  }

  clearAll() {
    this.searchObject = {};
    this.dataSource = new ExpenseDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
    this.supplierManagement = new SupplierManagement();
  }

  //     @Output() confirm = new EventEmitter<any>();
  //
  //   constructor(private el: ElementRef) {
  //   }
  //
  //   @HostListener('click')
  //    onClick() {
  //      $.confirm({
  //      buttons: {
  //     confirm: () => this.confirm.emit()
  //   }
  //  });
  // }

  dataList: any = [];

  selectedSupplier(item: SupplierManagement) {
    this.searchObject.supplierId = item.id;
    this.dataSource = new ExpenseDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }
  //ngOnInit(){
  //    Observable.fromEvent(this.filter.nativeElement, 'keyup')
  //        .debounceTime(100)
  //        .distinctUntilChanged()
  //        .subscribe(() => {
  //          if (!this.dataSource) { return; }
  //          this.dataSource.filter = this.filter.nativeElement.value;
  //        });
  //}
  //searchObject: any = {}
  //  dataList: any = []
  edit() {
    this.appService.navigate("./admin/expense-management", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogContentExampleDialogExpense, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  delete() {
    this.openDialog({ id: [this.selectedItem.id] });
  }

  payModeList: any[] = [];
  payModeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.payModeList = results["PayMode"];
          console.log("payModeList");
        }
      });
  }
  periodList: any[] = [];
  periodLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.periodList = results["Period"];
        // this.sortBy('value');
      });
  }
  // sortBy(field: string) {
  //   this.periodList.sort((a: any, b: any) => {
  //     if (a[field] < b[field]) {
  //         return -1;
  //     } else if (a[field] > b[field]) {
  //         return 1;
  //     } else {
  //         return 0;
  //     }
  // });
  // this.periodList = this.periodList;
  // }
  rangeList: any[] = [];
  rangeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.rangeList = results["Range"];
      });
  }

  retriveSuppliers() {
    this.restService.retriveSuppliers().subscribe(result => {
      if (result) {
        this.suppliers = result;
        this.suppliers.forEach(item => {
          item.displayName =
            item.firstName + " " + item.middleName + " " + item.lastName;
        });
      }
    });
  }

  filteredSuppliers: any[] = [];

  filterSuppliers(val: string) {
    this.filteredSuppliers =
      val && val != ""
        ? this.suppliers.filter(
            s => s.firstName.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.suppliers;
  }

  onSelectedItem(row) {
    this.selectedItem = row;
  }

  callDataSource() {
    if (
      this.searchObject.fromDate &&
      typeof this.searchObject.fromDate == "object"
    ) {
      this.searchObject.fromDate = Util.ISODate(
        this.searchObject.fromDate.toISOString()
      );
    }
    if (
      this.searchObject.toDate &&
      typeof this.searchObject.toDate == "object"
    ) {
      this.searchObject.toDate = Util.ISODate(
        this.searchObject.toDate.toISOString()
      );
    }
    this.dataSource = new ExpenseDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }

  print() {
    //    this.restService.paymentPrintPDF(this.selectedItem.id);
    this.restService.expensePrint(this.selectedItem.id);
  }
  open() {
    this.restService.expenseOpen(this.selectedItem.id);
  }
  view() {
    this.appService.navigate("./admin/expense-managementView", [
      { id: this.selectedItem.id }
    ]);
  }
  download() {
    this.restService.expenseDownload(this.selectedItem.id);
  }

  sendEmail() {
    this.showSide = true;
    // if (!item) {
    let item = new Email();
    // }
    this.email = Object.assign({}, item);
    this.email.to = this.selectedItem.emailId;
  }
  onClose(item: any) {
    this.showSide = false;
  }
  emailSave() {
    this.email.from = this.appService.getSessionUser().email;
    this.email.fileName = "Expense.pdf";
    this.email.content = btoa(this.email.message);
    delete this.email.message;

    this.restService
      .expenseEmail(this.selectedItem.id, this.email)
      .subscribe(result => {
        this.showSide = false;
      });
  }

  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/expense-managementView", [
      { id: this.selectedItemForView.id }
    ]);
  }
}



