import { StockItem } from "./../../../../entities/stockitem.entity";
import { AppService } from "app/shared/service/app.service";
import { Component, OnInit, SimpleChanges } from "@angular/core";
import { RestService } from "app/common/rest.service";
import { StockItemFormGroup } from "app/forms/stockitem.form";
import { isArray } from "util";

@Component({
  selector: "app-addstockitem",
  templateUrl: "./addstockitem.component.html",
  styleUrls: ["./addstockitem.component.scss"]
})
export class AddstockitemComponent implements OnInit {
  uqcList: any[] = [];
  categoryList: any[] = [];
  companyList: any[] = [];
  brandList: any[] = [];
  isRecordFound = false;
  manufacturerList: any[] = [];
  productItems: any[] = [];
  taxTypes: any[] = [];
  filteredUqcList: any[] = [];
  filteredCategoryList: any[] = [];
  filteredCompanyList: any[] = [];
  filteredBrandList: any[] = [];
  filteredManufacturerList: any[] = [];
  isView: boolean = false;
  isDisabled: boolean = false;
  disabled = true;

  productForm: any = StockItemFormGroup.init();
  detailsForm: any = StockItemFormGroup.init();
  pricingForm: any = StockItemFormGroup.init();

  stockItem: StockItem;
  isEditable: string = "true";
  isEdit: boolean = false;
  min: Date;
  max: Date;
  minimum: number;
  maximum: number;
  currentStep: number = 1;
  step1: any;
  step2: any;
  step3: any;
  step4: any;

  isActive: boolean = false;
  showMsg: boolean = false;
  showMs: boolean = false;
  showM: boolean = false;
  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    StockItemFormGroup.productGroup(this.productForm);
    StockItemFormGroup.detailsGroup(this.detailsForm);
    StockItemFormGroup.pricingGroup(this.pricingForm);
  }

  ngOnInit() {
    this.init();

    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrieveStockItem(id);
      if (!(this.isEditable == "true")) {
        this.isView = true;
        this.isDisabled = true;
      }
    } else {
      this.isEdit = false;
      this.isEditable = "true";
      this.disabled = !this.disabled;
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    this.stockItem = changes["stockItem"].currentValue;
  }

  private init() {
    this.loadUQC();
    this.loadCategories();
    this.loadCompanies();
    this.loadBrands();
    this.loadManufactures();
    this.initSotckItem();
    this.loadTaxTypes();
    this.loadProductItems();
    this.loadSuppliers();
    // this.loadProductItems();
  }

  loadTaxTypes() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.taxTypes = results["Tax"];
        console.log(this.taxTypes);
      });
  }

  private initSotckItem() {
    this.stockItem = new StockItem();
    this.stockItem.stockItemPrice.minStock = 0;
    this.stockItem.active = true;
    this.stockItem.companyId = this.appService.getBranch().id;
  }
  done() {
    this.appService.navigate("/admin/stockitem-search", []);
  }
  retrieveStockItem(id: string) {
    this.restService.getStockItem(id).subscribe(result => {
      if (result) {
        this.stockItem = result;
        this.supplierSelected =
          this.stockItem.stockItemPrice.supplier.companyName +
          " " +
          this.stockItem.stockItemPrice.supplier.mobileNumber;
        this.stockItem.companyId = this.appService.getBranch().id;
        this.isEdit = true;
      }
      this.isRecordFound = true;
    });
  }

  private loadUQC() {
    this.restService
      .dataloadFromFile("./assets/json/unit_quantity_codes.json")
      .subscribe(results => {
        if (results) {
          this.uqcList = results["unitQuantityCodes"];
        }
      });
  }

  private loadCategories() {
    this.restService.getStockItemCategories({}).subscribe(results => {
      console.log(results);
      if (results && isArray(results)) {
        this.categoryList = results;
      }
    });
  }

  private loadCompanies() {
    this.restService.getStockItemCompanies({}).subscribe(results => {
      console.log(results);
      if (results && isArray(results)) {
        this.companyList = results;
      }
    });
  }

  private loadBrands() {
    this.restService.getStockItemBrands({}).subscribe(results => {
      console.log(results);
      if (results && isArray(results)) {
        this.brandList = results;
      }
    });
  }

  private loadManufactures() {
    this.restService.getStockItemManufacturers({}).subscribe(results => {
      console.log(results);
      if (results && isArray(results)) {
        this.manufacturerList = results;
      }
    });
  }

  // private loadProductItems() {
  //   this.restService.getProductItem({}).subscribe(results => {
  //     console.log(results);
  //     if (results && isArray(results)) {
  //       this.stockItem.productName = results;
  //       this.stockItem.brandName = "True";
  //       console.log(this.productItems);
  //     }
  //   });
  // }

  private loadProductItems() {
    this.restService.getProductItem({}).subscribe(results => {
      console.log(results);
      if (results && isArray(results)) {
        this.productItems = results;
        console.log(this.productItems);
      }
    });
  }

  filterUQCList(val: string) {
    if (this.isEditable == "true") {
      this.filteredUqcList =
        val && val != ""
          ? this.uqcList.filter(
              s => s.description.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.uqcList;
    } else {
      this.filteredUqcList = [];
    }
  }

  filterCategoryList(val: string) {
    if (this.isEditable == "true") {
      this.filteredCategoryList =
        val && val != ""
          ? this.categoryList.filter(
              s => s.name.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.categoryList;
    } else {
      this.filteredCategoryList = [];
    }
  }

  filterCompanyList(val: string) {
    if (this.isEditable == "true") {
      this.filteredCompanyList =
        val && val != ""
          ? this.companyList.filter(
              s => s.name.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.companyList;
    } else {
      this.filteredCompanyList = [];
    }
  }

  filterBrandList(val: string) {
    if (this.isEditable == "true") {
      this.filteredBrandList =
        val && val != ""
          ? this.brandList.filter(
              s => s.name.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.brandList;
    } else {
      this.filteredBrandList = [];
    }
  }

  filterManufacturerList(val: string) {
    if (this.isEditable == "true") {
      this.filteredManufacturerList =
        val && val != ""
          ? this.manufacturerList.filter(
              s => s.name.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.manufacturerList;
    } else {
      this.filteredManufacturerList = [];
    }
  }
  filtername(val: string) {}

  changeEvent(event1: any, event2: any, event3: any) {
    console.log("activeEvent all steps");
    this.step1 = event1;
    this.step2 = event2;
    this.step3 = event3;
    setTimeout(() => {
      if (!this.isActive) {
        this.isActive = true;
        this.changeStep();
      }
    }, 100);
  }
  changeStep() {
    this.step1.disabled = true;
    this.step2.disabled = true;
    this.step3.disabled = true;

    this.step1.active = false;
    this.step2.active = false;
    this.step3.active = true;

    let activeStep: any = this.getActiveStep();
    activeStep.disabled = false;
    activeStep.active = true;
  }

  getActiveStep() {
    switch (this.currentStep) {
      case 1:
        return this.step1;
      case 2:
        return this.step2;
      case 3:
        return this.step3;
      default:
        return this.step1;
    }
  }

  detailsEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 2;
    this.changeStep();
  }
  pricingEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 3;
    this.changeStep();
  }

  backEvent() {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "none";
    this.currentStep = this.currentStep - 1;
    this.changeStep();
  }

  backToFirstStemp() {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "none";
    this.currentStep = 1;
    this.changeStep();
  }

  selectCategory(item: any) {
    if (item && typeof item == "object") {
      this.stockItem.categoryId = item.id;
      this.stockItem.categoryName = item.name;
    } else {
      this.filteredCategoryList.push({ id: null, name: item });
      this.stockItem.categoryName = item;
      this.stockItem.categoryId = null;
    }
  }

  selectCompany(item: any) {
    if (item && typeof item == "object") {
      this.stockItem.prodCompanyId = item.id;
      this.stockItem.prodCompanyName = item.name;
    } else {
      this.stockItem.prodCompanyName = item;
      this.stockItem.prodCompanyId = null;
    }
  }

  selectBrand(item: any) {
    if (item && typeof item == "object") {
      this.stockItem.brandId = item.id;
      this.stockItem.brandName = item.name;
    } else {
      this.stockItem.brandName = item;
      this.stockItem.brandId = null;
    }
  }

  selectManufacturer(item: any) {
    if (item && typeof item == "object") {
      this.stockItem.manfId = item.id;
      this.stockItem.manfName = item.name;
    } else {
      this.stockItem.manfName = item;
      this.stockItem.manfId = null;
    }
  }

  saveStockItem() {
    console.log(this.stockItem);
    this.stockItem.stockItemPrice.stockathand = this.stockItem.stockItemPrice.initialQty;
    this.stockItem.stockItemPrice.stockasofdate = new Date();
    this.restService.saveStockItem(this.stockItem).subscribe(response => {
      let message = "-----------------";
      if (this.isEdit) {
        message = "Stock Item updated successfully.";
      } else {
        message = "Stock Item  saved successfully. ";
        //this.expenseManagement = result;
      }

      this.appService.showMessage(message);

      if (this.isRecordFound) {
        this.appService.navigate("admin/stockitem-search", []);
      } else {
        this.appService.navigate("admin/stockitem-search", []);
      }
    });
  }

  public greaterThanValue;
  public lessThanValue;
  public lesserThanValue;
  public isInvalid: boolean;
  public isvalid: boolean;

  public onChange(event: any): void {
    this.isInvalid =
      this.stockItem.stockItemPrice.mrp <
      this.stockItem.stockItemPrice.itemCostPrice;
    this.isvalid =
      this.stockItem.stockItemPrice.mrp <
      this.stockItem.stockItemPrice.sellingPrice;
  }

  comparePaymentDate() {
    let payDate = this.stockItem.stockItemPrice.mrpDateTo;
    let chequeDate = this.stockItem.stockItemPrice.mrpDateFrom;

    //if(chequeDate !== null){
    if (chequeDate > payDate) {
      this.showMsg = true;
    } else {
      this.showMsg = false;
    }
  }
  comparePayment() {
    let cpdate = this.stockItem.stockItemPrice.cpDateFrom;
    let cpto = this.stockItem.stockItemPrice.cpDateTo;
    if (cpdate > cpto) {
      this.showMs = true;
    } else {
      this.showMs = false;
    }
  }
  compare() {
    let sellingfrom = this.stockItem.stockItemPrice.spDateFrom;
    let sellingto = this.stockItem.stockItemPrice.spDateTo;
    if (sellingfrom > sellingto) {
      this.showM = true;
    } else {
      this.showM = false;
    }
  }

  filteredSuppliers: any[] = [];
  supplierList: any[] = [];
  supplierSelected: string = "";

  loadSuppliers() {
    this.restService.searchSupplier({}).subscribe(results => {
      if (results) {
        this.supplierList = results;
      }
    });
  }

  filterSupplier(val: string) {
    if (this.isEditable == "true") {
      this.filteredSuppliers =
        val && val != ""
          ? this.supplierList.filter(
              s =>
                s.companyName.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
                s.mobileNumber.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.supplierList;
    } else {
      this.filteredSuppliers = [];
    }
  }
  selectedSupplier(item: any) {
    console.log(item);
    if (item && typeof item == "object") {
      this.supplierSelected = item.companyName + "-" + item.mobileNumber;
      this.stockItem.stockItemPrice.supplier = item;
    } else if (!isNaN(item)) {
      this.supplierSelected = "";
    } else {
      this.supplierSelected = "";
    }
  }
  // retrieveSupplier(id: string) {
  //   this.restService.getSupplier({ id: id }).subscribe(results => {
  //     this.stockItem = results;

  //     this.supplierSelected =
  //       this.stockItem.stockItemPrice.supplier.companyName +
  //       " " +
  //       this.stockItem.stockItemPrice.supplier.mobileNumber;
  //     this.isEdit = true;
  //   });
  // }
}



