import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { PaymentFormGroup } from "../../../forms/payment.form";
import { Payment } from "../../../entities/payment";
import { Item } from "./../../../entities/item.entity";
import { Customer } from "../../../entities/customer.entity";
import { Sales } from "./../../../entities/sales.entity";

import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/operator/map";
import { FormControl } from "@angular/forms";

@Component({
  selector: "app-payment",
  templateUrl: "./pm.payment.component.html"
})
export class PmPaymentComponent implements OnInit {
  myForm: any = PaymentFormGroup.init();
  payment: Payment;
  showElectric = false;
  showCheque = false;
  isRecordFound = false;
  customer: Customer;
  customers: Customer[] = [];
  sales: Sales;
  salesLi: any[] = [];
  sel = false;
  aaaaaamount = [];
  showControls: boolean = false;
  controls: any;
  isEdit: boolean = false;
  //     paymentamount:number;

  //     @Output()
  //     messageChange = new EventEmitter<number>();

  //     @Input()
  //   get message(){
  //     return this.paymentamount;
  //   }

  //   set message(val) {
  //     this.paymentamount > val;
  //     this.messageChange.emit(this.paymentamount);
  //   }

  //     isPrintEnable = fals;

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    this.customer = new Customer();
    this.sales = new Sales();
    this.payment = new Payment();

    PaymentFormGroup.edit(this.myForm);

    let id = this.appService.getParam("id");
    if (id !== undefined) {
      this.paymentDetails(id);
    } else {
      this.isEdit = true;
    }
  }
  ngOnInit() {
    this.retriveCustomer();
    this.payModeLoad();
    this.bankLoad();
  }

  paymentDetails(id) {
    this.restService.paymentEdit(id).subscribe(result => {
      if (result) {
        this.payment = result;
        if (this.payment.customer.businessName != null) {
          if (this.payment.customer.businessName.length == 0) {
            this.payment.customer.displayName =
              this.payment.customer.firstName +
              " " +
              this.payment.customer.lastName +
              " " +
              this.payment.customer.mobile;
          } else {
            this.payment.customer.displayName =
              this.payment.customer.businessName +
              " " +
              this.payment.customer.mobile;
          }
        } else {
          this.payment.customer.displayName =
            this.payment.customer.businessName +
            " " +
            this.payment.customer.mobile;
          this.payment.customer.displayName =
            this.payment.customer.firstName +
            " " +
            this.payment.customer.lastName +
            " " +
            this.payment.customer.mobile;
        }
        this.retriveInvoices(this.payment.id);

        this.isRecordFound = true;
      }
    });
  }

  onPayModeChange(val: string) {
    if (val == "Cheque") {
      PaymentFormGroup.cheque(this.myForm);
      this.showCheque = true;
      this.showElectric = false;
      this.payment.referenceNo = null;
    } else if (val == "Electronic") {
      PaymentFormGroup.electric(this.myForm);
      this.showElectric = true;
      this.showCheque = false;
      this.payment.bankName = null;
      this.payment.chequeNumber = null;
      this.payment.chequeDate = null;
    } else {
      PaymentFormGroup.cash(this.myForm);
      this.showCheque = false;
      this.showElectric = false;
      this.payment.bankName = null;
      this.payment.chequeNumber = null;
      this.payment.chequeDate = null;
      this.payment.referenceNo = null;
    }
  }

  save() {
    this.payment.companyId = this.appService.getBranch().id;
    this.restService.paymentSave(this.payment).subscribe(result => {
      //   this.payment = result;
      let message = "-----------------";
      if (this.isRecordFound) {
        message = "Payment receipt updated successfully.";
        this.appService.navigate("./admin/payment-search", [
          { id: this.payment.id }
        ]);
      } else {
        this.appService.showMessage(result.error);
        message = "Payment receipt saved successfully.";
      }
      this.appService.showMessage(message);
      if (this.isRecordFound) {
        this.myForm.reset();
        // this.appService.navigate('./admin/payment-search', []);
      } else {
        this.appService.navigate("./admin/payment-search", [
          { id: this.payment.id }
        ]);
      }
    });
  }

  retriveCustomer() {
    this.restService
      .searchUnpaidCutomerFrmInvoiceForPayment()
      .subscribe(result => {
        if (result) {
          this.customers = result;
          this.customers.forEach(item => {
            item.gstRegNumber =
              item.gstRegNumber == "" ? "Not Registered" : item.gstRegNumber;
            if (item.businessName != null) {
              if (item.businessName.length == 0) {
                item.displayName =
                  item.firstName + " " + item.lastName + " " + item.mobile;
              } else {
                item.displayName = item.businessName + " " + item.mobile;
              }
            } else {
              item.displayName =
                item.firstName + " " + item.lastName + " " + item.mobile;
            }

            // item.inlineAddress =
            //   item.address.lane +
            //   " " +
            //   item.address.area +
            //   " " +
            //   item.address.city +
            //   " " +
            //   item.address.state;
          });
        }
      });
  }

  //    filteredSales: any[] = [];
  //
  //    filterSales(val: string) {
  //        this.filteredSales = (val && val != '') ? this.salesLi.filter(s => s.invoiceNumber.toLowerCase().indexOf(val.toLowerCase()) >= 0)
  //            : this.salesLi;
  //    }

  selectedSales(item: Sales) {
    this.sales = item;
    this.payment.amount = item.total - item.paidAmount;
  }

  CalculateAmount() {
    let oldAmount = this.payment.amount;
    let newAmount = this.controls.amount;
    if (oldAmount < newAmount) {
      this.showControls = true;
    } else {
      this.showControls = false;
    }
  }

  filteredCustomers: any[] = [];

  filterCustomer(val: string) {
    this.filteredCustomers =
      val && val != ""
        ? this.customers.filter(
            s =>
              s.mobile.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
              s.businessName.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.customers;
  }

  // filterCustomer(val: string) {
  //     //alert(this.customers);
  //     this.filteredCustomers = (val && val != '') ? this.customers.filter(s => s.businessName.toLowerCase().indexOf(val.toLowerCase()) >= 0)
  //         : this.customers;
  // }

  selectedCustomer(item: Customer) {
    this.customer = item;
    this.payment.customer = this.customer;
    if (this.payment.customer.businessName != null) {
      if (this.payment.customer.businessName.length == 0) {
        this.payment.customer.displayName =
          this.payment.customer.firstName +
          " " +
          this.payment.customer.lastName +
          " " +
          this.payment.customer.mobile;
      } else {
        this.payment.customer.displayName =
          this.payment.customer.businessName +
          " " +
          this.payment.customer.mobile;
      }
    } else {
      this.payment.customer.displayName =
        this.payment.customer.businessName + " " + this.payment.customer.mobile;
      this.payment.customer.displayName =
        this.payment.customer.firstName +
        " " +
        this.payment.customer.lastName +
        " " +
        this.payment.customer.mobile;
    }

    this.retriveInvoices(item.id);
    console.log(item);
  }
  customerId: number = 0;
  retriveInvoices(customerId) {
    this.salesLi = null;
    this.restService
      .searchInvoiceByCutomerIdForPayment(customerId)
      .subscribe(result => {
        if (result) {
          this.salesLi = result;
          this.payment.invoiceNumber = result.invoiceNumber;
        }
      });
  }

  bankList: any[] = [];
  bankListTemp: any[] = [];
  bankLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.bankList = results["Bank"];
        this.bankListTemp = results;
      });
  }

  filteredbanks: any[] = [];

  filterbanks(val: string) {
    console.log(val);
    this.filteredbanks =
      val && val != ""
        ? this.bankList.filter(
            s => s.value.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.bankList;
  }

  //   search(): void {
  //     let term = this.bankList;
  //     this.bankList = this.bankListCopy.filter(function(tag) {
  //         return tag.name.indexOf(term) >= 0;
  //     });
  // }

  payModeList: any[] = [];
  payModeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.payModeList = results.PayMode;
        }
      });
  }
  printPriview() {
    //    this.restService.invoiceOpen(this.sales.id);
  }
  onlyNumberKey(event) {
    return event.charCode == 8 || event.charCode == 0
      ? null
      : event.charCode >= 48 && event.charCode <= 57;
  }
}



