import { Component, OnInit, Input, SimpleChanges } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { Prop } from "../../../common/props";
import { Util } from "../../../shared/utils/util";

import { ServiceFormGroup } from "../../../forms/Service.form";

import { Service } from "../../../entities/Service.entity";

@Component({
  selector: "app-service",
  templateUrl: "./service.edit.component.html"
})
export class ServiceEditComponent implements OnInit {
  myForm: any = ServiceFormGroup.init();
  service: Service = new Service();
  @Input() id;
  serviceTypeList: any[] = [];
  incomeAmountList: any[] = [];
  taxList: any[] = [];
  uqcList: any[] = [];
  filteredUqcList: any[] = [];
  taxTypes: any[] = [];
  isEditable: string = "true";
  isEdit: boolean = false;
  serviceitem: Service;
  isView: boolean = false;
  isDisabled: boolean = false;
  disabled = true;
  isRecordFound = false;
  serviceActive: any;

  constructor(
    private appService: AppService,
    private restService: RestService // vcr: ViewContainerRef
  ) {
    ServiceFormGroup.edit(this.myForm);
   
    // // this.service.productType = "Service";
    // // this.isEditable = this.appService.getParam("isEditable");
    // // let id = this.appService.getParam("id");
    // // if (id !== undefined) {
    // //   this.retrieve(id);
    // }

    // this.toastr.setRootViewContainerRef(vcr);
    //this.showWarning();
  }

  // showWarning() {
  //     this.toastr.warning('You are being warned.', 'Alert!');
  // }

  ngOnInit() {
    this.init();
    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrieveService(id);
      if (!(this.isEditable == "true")) {
        this.isView = true;
        this.isDisabled = true;
      }
    } else {
      this.isEdit = false;
      this.isEditable = "true";
      this.disabled = !this.disabled;
    }
    // if(this.service.active == true){
      this.service.active == true ? "Active" : "Inactive";
      console.log(this.service.active)
    // }
  }

  ngOnChanges(changes: SimpleChanges) {
    this.serviceitem = changes["serviceItem"].currentValue;
  }

  private init() {
    this.taxLoad();
    this.uqcLoad();
    this.loadTaxTypes();
  }

  // private serviceTypeLoad() {
  //   this.restService.dataload("/ServiceType").subscribe(results => {
  //     if (results) {
  //       this.serviceTypeList = results;
  //     }
  //   });
  // }

  // private incomeAmountLoad() {
  //   this.restService.dataload("/IncomeAccount").subscribe(results => {
  //     if (results) {
  //       this.incomeAmountList = results;
  //     }
  //   });
  // }

  retrieveService(id: string) {
    this.restService.getService(id).subscribe(result => {
      if (result) {
        this.service = result;
        console.log(this.service)
        this.service.companyId = this.appService.getBranch().id;
        if(this.service.active == "true"){
          this.service.active = "Active"
        console.log(this.service.active);
        }
        else{
          this.service.active = "Inactive"
          console.log(this.service.active);
        }
        
        this.isEdit = true;
      }
      this.isRecordFound = true;
    });
  }

  private taxLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        if (results) {
          this.taxList = results["Tax"];
        }
      });
  }

  private uqcLoad() {
    this.restService
      .dataloadFromFile("./assets/json/unit_quantity_codes.json")
      .subscribe(results => {
        if (results) {
          this.uqcList = results["unitQuantityCodes"];
        }
      });
  }

  // filterUQCList(val: string) {
  //   if (val) {
  //     this.service.uqc = val;
  //   }
  //   this.filteredUqcList =
  //     val && val != ""
  //       ? this.uqcList.filter(
  //           s => s.description.toLowerCase().indexOf(val.toLowerCase()) >= 0
  //         )
  //       : this.uqcList;
  // }

  loadTaxTypes() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.taxTypes = results["Tax"];
        console.log(this.taxTypes);
      });
  }

  retrieve(id: string) {
    this.restService.serviceEntity(id).subscribe(result => {
      if (result) {
        this.service = result;
        this.service.companyId = this.appService.getBranch().id;
        this.isEdit = true;
      }
      this.isRecordFound = true;
    });
  }

  valid() {
    this.service.active = true;
    //this.service.serviceCode = ""+ Math.floor((Math.random()*9999+1));
    this.service.productType = "Service";
    this.service.companyId = this.appService.getBranch().id;
    return true;
  }

  save() {
    if (!this.valid()) return;
    this.service.productType = "Service";
    this.service.companyId = this.appService.getBranch().id;
    this.service.active = true;
    this.restService.serviceSave(this.service).subscribe(result => {
      // if (result) {
      //   this.service = new Service();
      //   this.service.productType = "Service";
      //   this.appService.showMessage("Saved successfully.");
      //   this.myForm.reset();
      //   this.appService.navigate("/admin/serviceitem-search", []);
      // }
      let message = "-----------------";
      if (this.isEdit) {
        message = "Service Order updated successfully.";
        //this.expenseManagement = result;
      } else {
        message = "Service Item saved successfully.";
      }
      this.appService.showMessage(message);
      this.appService.navigate("/admin/serviceitem-search", []);
    });
  }

  add() {
    this.service = new Service();
    this.myForm = ServiceFormGroup.init();
  }

  back() {
    this.appService.navigate("", []);
  }
  done() {
    this.appService.navigate("/admin/serviceitem-search", []);
  }
}



