import { CRDNDeleteDialog } from "./crdn-delete-dialog.component";
import { DialogContentExampleDialogCustomer } from "./../../custommangement/DialogContentExampleDialogCustomer";
import { MdSort, MdPaginator, MdDialog } from "@angular/material";
import { Component, OnInit, ViewChild, AfterViewInit } from "@angular/core";

import { RestService } from "app/common/rest.service";
import { AppService } from "app/shared/service/app.service";
import { CRDNDataSource } from "./crdn-search.datasource";
import { analyzeAndValidateNgModules } from "@angular/compiler";
import { CRDNFormGroup } from "app/forms/crdn.form";
import { FormBuilder, FormGroup, FormControl } from "@angular/forms";
import { Storage } from "../../../../shared/utils/storage";
import { CrdnDialogContentComponent } from "./crdn-dialog-content.component";

@Component({
  selector: "app-crdn-search",
  templateUrl: "./crdn-search.component.html",
  styleUrls: ["./crdn-search.component.scss"]
})
export class CrdnSearchComponent implements OnInit {
  crdnFrom: FormGroup;
  searchObj: any = {};
  documentTypes: any[] = [];
  selectedItemForView: any = null;
  selectedItem: any = null;
  searchField: FormControl;

  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;
  dataSource: CRDNDataSource = null;
  displayedColumns: any[] = [
    "crdnNumber",
    "documentType",
    "receiverName",
    "refundVoucherValue",
    "action"
  ];
  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {
    // this.crdnFrom = new FormBuilder().group({
    //   "crdnNumber": this.searchObj.crdnNumber
    //   });
    //   this.crdnFrom.valueChanges
  }

  ngOnInit() {
    this.searchField = new FormControl();
    this.documentTypes = this.appService.getcrdnDocTypes();
    this.searchObj.crdnType = null;
    this.searchObj.crdnNumber = null;
    this.callDataSource();
  }

  callDataSourceFilter() {
    console.log("executing crdn filter");
    //   this.dataSource.setSearchObj(this.se)
  }
  callDataSource() {
    this.dataSource = new CRDNDataSource(
      this.restService,
      this.searchObj,
      this.sort,
      this.paginator,
      this.searchField
    );
    // this.dataSource.getDataList();
  }

  selectItem(item: any) {
    this.selectedItem = item;
  }

  viewCRDN(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/crdn-edit", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);
    console.log(row);
    console.log(this.searchObj);
    // if (this.searchObj.type == "Inventory") {
    //   this.appService.navigate("./admin/inventory-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // } else if (this.searchObj.type == "NonInventory") {
    //   this.appService.navigate("./admin/noninventory-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // } else {
    //   this.appService.navigate("./admin/service-edit", [
    //     { id: this.selectedItemForView.id, isEditable: false }
    //   ]);
    // }
  }

  getDocTypeDescription(val: string) {
    let docType = this.documentTypes.filter(
      s => s.value.toLowerCase() == val.toLowerCase()
    );
    if (docType && docType[0]) return docType[0].name;
    else return "";
  }

  openDialog(id) {
    const dialogRef = this.dialog.open(CRDNDeleteDialog, {
      width: "500px",
      height: "250px",
      data: this.selectedItem.id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  pop() {
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }
  opens(id) {
    // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
    let companyInfo = Storage.getSessionUser();

    let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
    if (parseInt(pageLayoutId) == 1) {
      const dialogRef = this.dialog.open(CrdnDialogContentComponent, {
        width: "600px",
        data: id
      });
    }
  }

  public clearAll() {
    this.searchObj = {};
    this.dataSource = new CRDNDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator,
      this.searchField
    );
  }

  view() {
    this.appService.navigate("./admin/crdn-edit", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }

  edit() {
    this.appService.navigate("./admin/crdn-edit", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  delete() {
    this.openDialog({ id: this.selectedItem.id });
  }
}



