import { ServiceItemDataSource } from "./service-item-listing.datasource";

import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { RestService } from "app/common/rest.service";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import { AppService } from "app/shared/service/app.service";
import {
  MdDialog,
  MdSort,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { DialogDeleteServiceItem } from "./dialog-delete-service-item";

@Component({
  selector: "app-service-item-listing",
  templateUrl: "./service.item.listing.component.html",
  styleUrls: ["./service.item.listing.component.scss"]
})
export class ServiceItemListingComponent implements OnInit {
  selectedItem: any = null;
  selectedItemForView: any;

  @ViewChild("filter") filter: ElementRef;
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;

  dataSource: ServiceItemDataSource = null;
  //   displayedColumns: any[] = [
  //     "productName",
  //     "brandName",
  //     "categoryName",
  //     "mrpPrice",
  //     "costPrice",
  //     "sellingPrice",
  //     "stockAtHand",
  //     "action"
  //   ];

  displayedColumns: any[] = [
    "serviceName",
    // "uqc",
    //"tax",
    "salesUnit",
    "action"
  ];

  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {}

  ngOnInit() {
    this.dataSource = new ServiceItemDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
    Observable.fromEvent(this.filter.nativeElement, "keyup")
      .debounceTime(100)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) {
          return;
        }
        this.dataSource.filter = this.filter.nativeElement.value;
      });
  }

  searchObj: any = {};

  onKeydown(filterValue: string) {
    if (filterValue.length < 3) return;
    if (filterValue.length < 4) {
      // For First three letters no search - display all
      this.clearAll();
    } else this.ApplyFilter(filterValue);
  }
  public ApplyFilter(filterValue: string) {
    this.searchObj = {};
    this.dataSource = new ServiceItemDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
    filterValue = filterValue.trim().toLowerCase();
    this.dataSource.filter = filterValue;
  }
  dataList: any = [];

  selectItem(item: any) {
    this.selectedItem = item;
  }
  onSelectedItem(row) {
    this.selectedItem = row;
  }

  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/service-edit", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);

    console.log(row);
    console.log(this.searchObj);
  }

  view(item: any) {
    this.appService.navigate("/admin/service-edit", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }

  edit(item: any) {
    this.appService.navigate("/admin/service-edit", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  //For delete Popup

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogDeleteServiceItem, {
      width: "500px",
      height: "250px",
      data: id
    });
    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }
  public clearAll() {
    this.searchObj = {};
    this.dataSource = new ServiceItemDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
  }

  delete(item: any) {
    this.openDialog(this.selectedItem.id);
  }
}



