import { Component, OnInit, Input } from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { ReportFilter } from "app/entities/report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
declare let google: any;

@Component({
  selector: "app-stock-pie-chart",
  templateUrl: "./stock-pie-chart.component.html",
  styleUrls: ["./stock-pie-chart.component.scss"]
})
export class StockPieChartComponent implements OnInit {
  mode: string;
  totalStockAtHand: any;
  totalStockSold: any;
  stockAtHandPercent: any;
  stockSoldPercent: any;
  x: any;
  data: any;
  reportFilter: WebReportFilter = new WebReportFilter();

  @Input()
  monthsList: any[];
  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    this.stockLoad();
  }

  ngOnInit() {
    google.charts.load("current", { packages: ["corechart"] });
    google.charts.setOnLoadCallback(this.drawChart1);
    this.x = setInterval(() => {
      this.drawChart1();
    }, 1000);
    // this.pauseTimeLine(this.x);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }

  drawChart1 = () => {
    var data = google.visualization.arrayToDataTable([
      ["Task", "Hours per Day"],
      ["Stock Sold", this.stockSoldPercent],
      ["Stock at Hand", this.stockAtHandPercent]
    ]);
    var options = {
      colors: ["#f2aa2b", "#009688"],
      pieHole: 0.4
    };
    var chart = new google.visualization.PieChart(
      document.getElementById("piechart1")
    );
    chart.draw(data, options);
  };
  onSelectMonth(item) {
    this.reportFilter.month = item.key;
    // console.log(item.key);
    this.stockLoad();
  }
  stockLoad() {
    this.reportFilter.report = "report=DB_STOCKATHAND_AND_SOLD";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        // console.log(results);
        this.totalStockAtHand = results[0].StockatHand;
        this.totalStockSold = results[0].TotalStockSold;
        this.stockAtHandPercent =
          (this.totalStockAtHand /
            (this.totalStockAtHand + this.totalStockSold)) *
          100;
        this.stockSoldPercent = 100 - this.stockAtHandPercent;
        // console.log(this.stockAtHandPercent);
        // console.log(this.stockSoldPercent);
        this.drawChart1();
      });
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



