import { Component, OnInit, Input } from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { ReportFilter } from "app/entities/report-filter.entity";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
declare let google: any;

@Component({
  selector: "app-income-expenses",
  templateUrl: "./income-expenses.component.html",
  styleUrls: ["./income-expenses.component.scss"]
})
export class IncomeExpensesComponent implements OnInit {
  allMonths = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];
  x: any;
  finaldata: any;
  data: any;
  @Input()
  yearsList: any[];
  incomeData: any[] = [];
  monthly: any[];
  finalData: any[] = [];
  totalIncome: any = 0;
  totalExpense: any = 0;
  reportFilter: WebReportFilter = new WebReportFilter();
  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    this.incomeandexpensesLoad();
  }

  ngOnInit() {
    google.charts.load("current", { packages: ["corechart", "bar"] });
    google.charts.setOnLoadCallback(this.drawStuff);

    this.x = setInterval(() => {
      this.drawStuff();
    }, 1000);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }

  drawStuff = () => {
    var chartDiv = document.getElementById("chart_div");
    var data = new google.visualization.DataTable();
    data.addColumn("string", "Months");
    data.addColumn("number", "Income");
    data.addColumn("number", "Expense");
    data.addRows(this.finalData);
    var materialOptions = {
      colors: ["#8fe388", "#ffc107"],
      chart: {},
      series: {
        0: { axis: "distance" } // Bind series 0 to an axis named 'distance'.
      }
    };
    function drawMaterialChart() {
      var materialChart = new google.charts.Bar(chartDiv);
      materialChart.draw(
        data,
        google.charts.Bar.convertOptions(materialOptions)
      );
    }
    drawMaterialChart();
  };
  onSelectYear(item) {
    this.reportFilter.year = item;
    // console.log(item);
    this.finalData = [];
    this.totalIncome = 0;
    this.totalExpense = 0;
    this.incomeandexpensesLoad();
  }

  incomeandexpensesLoad() {
    this.reportFilter.report = "report=DB_INCOME_AND_EXPENSES";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        this.incomeData = results;
        this.monthSort();

        this.monthly.map(x => {
          this.totalIncome += x.TotalIncome;
          this.totalExpense += x.TotalExpenses;
          var xArray: any[] = [
            x.Expense_month_name,
            x.TotalIncome,
            x.TotalExpenses
          ];
          // console.log("xArray :: " + xArray);
          this.finalData.push(xArray);
        });

        // console.log("final chart data for income expense is ", +this.finalData);
        this.drawStuff();
      });
  }
  monthSort() {
    class incomeNewMonth {
      Expense_month_name: string;
      TotalExpenses: Number;
      TotalIncome: Number;
    }

    this.monthly = this.incomeData.sort((a, b) => {
      return (
        this.allMonths.indexOf(a.Expense_month_name) -
        this.allMonths.indexOf(b.Expense_month_name)
      );
    });
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



