import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";

export class ServiceFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }
  static edit(group: FormGroup) {
    group.addControl(
      "service_serviceName",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 30)
    );
    group.addControl(
      "service_serviceType",
      this.getController(null, this.TYPE_DATA, null, null)
    );
    group.addControl(
      "service_serviceCode",
      this.getController(null, this.TYPE_COMBI_NUM, null, null)
    );
    group.addControl(
      "service_productType",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    // group.addControl(
    //   "service_unitquantitycode",
    //   this.getController(true, this.TYPE_DATA, null, null)
    // );
    group.addControl(
      "service_salesUnit",
      this.getController(null, this.TYPE_DECIMAL, null, 30)
    );
    group.addControl(
      "service_incomeAmount",
      this.getController(null, this.TYPE_DECIMAL, null, null)
    );
    group.addControl(
      "service_abatement",
      this.getController(null, this.TYPE_NUMBER, null, 30)
    );
    group.addControl(
      "service_tax",
      this.getController(null, this.TYPE_DATA, null, null)
    );
    group.addControl(
      "status1",
      this.getController(null, this.TYPE_ALL, null, null)
    );
  }
}



