import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  SimpleChange
} from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
declare let google: any;

@Component({
  selector: "app-pie-chart",
  templateUrl: "./pie-chart.component.html",
  styleUrls: ["./pie-chart.component.scss"]
})
export class PieChartComponent implements OnInit {
  @Input()
  yearsList: any[];
  mode: string;
  x: any;
  expenseData: any[] = [];
  data: any;

  reportFilter: WebReportFilter = new WebReportFilter();
  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    this.expenseLoad();
  }

  ngOnInit() {
    google.charts.load("current", { packages: ["corechart"] });
    google.charts.setOnLoadCallback(this.drawChart);
    this.x = setInterval(() => {
      this.drawChart();
    }, 1000);
    // this.pauseTimeLine(this.x);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }

  drawChart = () => {
    var data = new google.visualization.DataTable();
    data.addColumn("string", "ExpenseType");
    data.addColumn("number", "ExpenseTotal");
    data.addRows(this.expenseData);

    var options = {
      colors: [
        "#FFA502",
        "#9966cb",
        "#296d98",
        "#57aac3",
        "#58bc82",
        "#6cc7e2"
      ],

      pieHole: 0.4
    };
    var chart = new google.visualization.PieChart(
      document.getElementById("piechart")
    );
    chart.draw(data, options);
  };

  onSelectYear(item) {
    this.reportFilter.year = item;
    // console.log(item);
    this.expenseLoad();
  }

  expenseLoad() {
    this.expenseData = [];
    this.reportFilter.report = "report=DB_EXPENSETYPES_TOTAL";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        // console.log(results);
        results.map(x => {
          var xArray: any[] = [x.ExpenseTypeName, x.ExpenseTypeTotal];
          this.expenseData.push(xArray);
          // console.log(this.expenseData);
        });

        this.drawChart();
      });
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



