import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import { AppFormGroup } from "../shared/common/app.form";

export class CustomerFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }
  static details(group: FormGroup) {
    group.addControl(
      "custom_title",
      this.getController(null, this.TYPE_DATA, null, 20)
    );
    group.addControl(
      "custom_firstName",
      this.getController(null, this.TYPE_ALFA, null, 50)
    );
    group.addControl(
      "custom_lastName",
      this.getController(null, this.TYPE_ALFA, null, 20)
    );
    group.addControl(
      "custom_displayName",
      this.getController(null, this.TYPE_BUSI_NAME, 3, 50)
    );
    group.addControl(
      "custom_email",
      this.getController(null, this.TYPE_EMAIL, null, 50)
    );
    group.addControl(
      "custom_website",
      this.getController(null, this.TYPE_WEBSITE, null, 99)
    );
    group.addControl(
      "custom_others",
      this.getController(null, this.TYPE_DATA, null, 99)
    );
    group.addControl(
      "custom_businessName",
      this.getController(null, this.TYPE_BUSI_NAME, 5, 50)
    );
    group.addControl(
      "custom_tan",
      this.getController(null, this.TYPE_ALFA_NUM, 10, 12)
    );
    group.addControl(
      "custom_fax",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, 10, 15)
    );
    group.addControl(
      "custom_phone",
      this.getController(null, this.TYPE_PHONE_NO, 10, 20)
    );
    group.addControl(
      "custom_mobile",
      this.getController(null, this.TYPE_MOBILE_NO, 10, 10)
    );
    group.addControl(
      "custom_discount",
      this.getController(null, this.TYPE_MOBILE_NO, 10, 10)
    );
  }

  static taxGroup(group: FormGroup) {
    group.addControl(
      "tax_taxRegistartionNo",
      this.getController(null, this.TYPE_NUMBER, 11, 11)
    );
    group.addControl(
      "tax_cstRegistartionNo",
      this.getController(null, this.TYPE_NUMBER, 10, 12)
    );
    group.addControl(
      "tax_gstNo",
      this.getController(false, this.GST_CODE_PATTERN, 15, 15)
    );
    group.addControl(
      "tax_pan",
      this.getController(null, this.TYPE_PAN_NO, 10, 10)
    );
    group.addControl(
      "tax_type",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 99)
    );

    group.addControl(
      "payment_paymentMethod",
      this.getController(null, this.TYPE_DATA, null, 20)
    );
    group.addControl(
      "payment_deliveryMethod",
      this.getController(null, this.TYPE_DATA, null, 20)
    );
    group.addControl(
      "payment_terms",
      this.getController(null, this.TYPE_DATA, null, 20)
    );
    group.addControl(
      "payment_openingBalance",
      this.getController(null, this.TYPE_DECIMAL, null, 15)
    );
    group.addControl(
      "payment_currentDateAs",
      this.getController(null, this.TYPE_DATE, null, 15)
    );
  }

  static address(group: FormGroup) {
    group.addControl(
      "custom_address_lane",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 50)
    );
    group.addControl(
      "custom_address_area",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 30)
    );
    group.addControl(
      "custom_address_city",
      this.getController(null, this.TYPE_ALFA_DES, 3, 30)
    );
    group.addControl(
      "custom_address_state",
      this.getController(null, this.TYPE_DATA, 2, 70)
    );
    group.addControl(
      "custom_address_street",
      this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 30)
    );
    group.addControl(
      "custom_address_zipCode",
      this.getController(null, this.TYPE_ZIP_CODE, 6, 6)
    );
  }

  static shippingAddress(group: FormGroup, add: boolean) {
    if (add) {
      group.addControl(
        "custom_shippingAddress_lane",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 50)
      );
      group.addControl(
        "custom_shippingAddress_area",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 50)
      );
      group.addControl(
        "custom_shippingAddress_city",
        this.getController(null, this.TYPE_ALFA_DES, null, 50)
      );
      group.addControl(
        "custom_shippingAddress_state",
        this.getController(null, this.TYPE_DATA, null, 50)
      );
      group.addControl(
        "custom_shippingAddress_street",
        this.getController(null, this.TYPE_ALFA_NUM_DES_SPACE, null, 50)
      );
      group.addControl(
        "custom_shippingAddress_zipCode",
        this.getController(null, this.TYPE_NUMBER, 6, 6)
      );
    } else {
      group.removeControl("custom_shippingAddress_lane");
      group.removeControl("custom_shippingAddress_area");
      group.removeControl("custom_shippingAddress_city");
      group.removeControl("custom_shippingAddress_state");
      group.removeControl("custom_shippingAddress_street");
      group.removeControl("custom_shippingAddress_zipCode");
    }
  }
}



