import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  Pipe,
  PipeTransform,
  Injectable
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { Address } from "./../../../entities/address.entity";
import { Account } from "./../../../entities/account.entity";
import { CustomerFormGroup } from "../../../forms/customer.form";
import { CustomManagement } from "../../../entities/custommanagement";

@Pipe({ name: "jsonFilterBy" })
@Injectable()
@Component({
  selector: "app-address",
  templateUrl: "./cm.address.component.html"
})
export class CmAdressComponent implements OnInit {
  // address:Address;
  myForm: any = CustomerFormGroup.init();
  isSameBillingAddress: boolean = true;
  isEditable: string = "true";

  @Input("account")
  account: Account;

  @Input()
  address: Address[] = [];

  @Input("customManagement")
  customManagement: CustomManagement;
  @Output("addressEvent")
  addressEvent = new EventEmitter<string>();
  @Output("backEvent")
  backEvent = new EventEmitter<string>();

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    CustomerFormGroup.address(this.myForm);
  }

  ngOnInit() {
    console.log("Account Address");
    this.stateLoad();

    //   this.filteredOptions = this.myControl.valueChanges
    //     .pipe(
    //       startWith(''),
    //       map(value => this._filter(value))
    //     );
    // }
  }

  next() {
    if (this.isSameBillingAddress) {
      this.customManagement.shippingAddress.lane = this.customManagement.billingAddress.lane;
      this.customManagement.shippingAddress.street = this.customManagement.billingAddress.street;
      this.customManagement.shippingAddress.area = this.customManagement.billingAddress.area;
      this.customManagement.shippingAddress.city = this.customManagement.billingAddress.city;
      this.customManagement.shippingAddress.state = this.customManagement.billingAddress.state;
      this.customManagement.shippingAddress.zipCode = this.customManagement.billingAddress.zipCode;
    }
    this.addressEvent.emit();
  }
  back() {
    this.backEvent.emit();
  }

  addressChange() {
    if (this.isSameBillingAddress) {
      CustomerFormGroup.shippingAddress(this.myForm, false);
      // this.customManagement.shippingAddress.lane = this.customManagement.billingAddress.lane;
      // this.customManagement.shippingAddress.street = this.customManagement.billingAddress.street;
      // this.customManagement.shippingAddress.area = this.customManagement.billingAddress.area;
      // this.customManagement.shippingAddress.city = this.customManagement.billingAddress.city;
      // this.customManagement.shippingAddress.state = this.customManagement.billingAddress.state;
      // this.customManagement.shippingAddress.zipcode = this.customManagement.billingAddress.zipcode;
    } else {
      CustomerFormGroup.shippingAddress(this.myForm, true);
    }
  }
  focusOutFunction() {
    let array: any = [];
    this.stateList.forEach(item => {
      if (this.customManagement.billingAddress.state == item[0]) {
        array.push(item[0]);
      }
    });
    console.log(array);
    if (array.length == 0) {
      // this.appService.showMessage('Please Select Correct State Value')
      this.customManagement.billingAddress.state = null;
    }
  }
  // cityList: any[] = [];
  // cityLoad() {
  //   this.restService.dataload('City')
  //     .subscribe( results => {
  //       this.cityList = results;
  //     })
  // }
  filteredStates: any[] = [];
  stateList: any[] = [];
  stateListTemp: any[] = [];
  stateLoad() {
    //this.restService.dataloadFromFile()
    this.restService
      .dataloadFromFile("./assets/json/states.json")
      .subscribe(results => {
        this.stateList = results["states"];
        console.log(this.stateList);
      });
  }

  // filterStates(val: string) {

  //   this.filteredStates = (val && val != '') ? this.stateList.filter(keyValues => keyValues[0].toLowerCase().startsWith(val.toLowerCase()))
  //     : this.stateList;
  // }
  selectState(state, form) {
    form.state = state;
  }
  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.key.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }

  selectedAddress(item: any) {
    if (item) {
      this.customManagement.billingAddress.state = item[0];
    }
  }
}



