import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from './../../../common/rest.service';
import { Prop } from './../../../common/props';
import { InvoiceFormGroup } from '../../../forms/invoice.form';
import { CustomManagement } from '../../../entities/custommanagement';
import { CustomerFormGroup } from '../../../forms/customer.form';
@Component({
  selector: 'app-contactdetails',
  templateUrl: './cm.contactdetails.component.html'

})

export class CmContactdetailsComponent implements OnInit {

  myForm: any = CustomerFormGroup.init();
  public isEditable = false;

  @Input('customManagement')
  customManagement: CustomManagement = new CustomManagement();

  @Output('contactEvent')
  contactEvent = new EventEmitter<string>();


  constructor(private appService: AppService, private restService: RestService) {
    CustomerFormGroup.details(this.myForm);
    this.isEditable = this.appService.getParam("isEditable");

   // this.titleLoad();
  }
  ngOnInit() {
    this.titleLoad();
    // console.log("Account Address");
  }

  next() {
    this.contactEvent.emit()
    
  }
    
  titleList: any[] = [];
  titleLoad() {
    this.restService.dataloadFromFile('./assets/json/common.json')
      .subscribe(results => {
        this.titleList = results["Title"];
        console.log(this.titleList)
      });
  }
    //  titleList = [
    //     { value: '0', viewValue: 'Mr' },
    //     { value: '1', viewValue: 'Mrs' }
    // ];

//  titleList: any[] = [];
//  titleLoad() {
//    this.restService.dataload('Title')
//      .subscribe(results => {
//        this.titleList = results;
//        console.log(this.titleList)
//      })
//  }

}





