import { Component, OnInit, Input } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { Prop } from "../../../common/props";
import { CustomerFormGroup } from "../../../forms/customer.form";
import { CustomManagement } from "../../../entities/custommanagement";
import { Address } from "../../../entities/address.entity";

@Component({
  selector: "customer-edit",
  templateUrl: "./customeredit.html"
})
export class CustomerEditComponent {
  mode: string;
  myForm: any = CustomerFormGroup.init();
  public isEditable = false;
  @Input()
  customerManagement: CustomManagement;
  isSameBillingAddress: boolean = false;
  customerType = ["Retail", "Credit"];
  paymentMethod = ["Cash", "Electronic", "Cheque"];
  stateList: any[] = [];
  filteredStates: any[] = [];

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    this.customerManagement = new CustomManagement();
    this.customerManagement.billingAddress = new Address();

    // this.customerManagement = new CustomManagement();
    CustomerFormGroup.details(this.myForm);
    CustomerFormGroup.address(this.myForm);
    CustomerFormGroup.taxGroup(this.myForm);
    let id = this.appService.getParam("id");
    this.customerEdit(id);
    //console.log(this.customerManagement.companyId);
  }

  customerEdit(id) {
    this.restService.retreiveCustomerById(id).subscribe(result => {
      if (result) {
        this.customerManagement = result;
      }
    });
  }

  ngOnInit() {
    //  this.paymentTypeLoad();
    this.stateLoad();
    this.changeAddress();
  }

  changeAddress() {
    if (this.customerManagement.isShippingAddress) {
      CustomerFormGroup.shippingAddress(this.myForm, false);
    } else {
      CustomerFormGroup.shippingAddress(this.myForm, true);
    }
  }

  focusOutFunction() {
    let array: any = [];
    this.stateList.forEach(item => {
      if (this.customerManagement.billingAddress.state == item[0]) {
        array.push(item[0]);
      }
    });
    console.log(array);
    if (array.length == 0) {
      // this.appService.showMessage('Please Select Correct State Value')
      this.customerManagement.billingAddress.state = null;
    }
  }
  save() {
    console.log(
      "this.customerManagement : " +
        this.customerManagement.billingAddress.state
    );
    this.customerManagement.companyId = this.appService.getBranch().id;
    console.log(this.customerManagement.companyId);
    console.log(this.restService);
    this.restService
      .customerUpdate(this.customerManagement)
      .subscribe(results => {
        if (results) {
          this.customerManagement = results;
          this.appService.showMessage("Updated successfully.");
          this.appService.navigate("/admin/customersearch", []);
        }
      });
  }
  finish() {
    this.appService.navigate("/admin/customersearch", []);
  }

  stateLoad() {
    //this.restService.dataloadFromFile()
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.stateList = results["stateList"];
        console.log(this.stateList);
      });
  }

  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.value.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }
}



