import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { CustomManagement } from "../../../entities/custommanagement";

@Component({
  selector: "app-custommangement",
  templateUrl: "./custommangement.component.html"
})
export class CustomMangementComponent {
  currentStep: number = 1;
  step1: any;
  step2: any;
  step3: any;
  customManagement: CustomManagement;
  isActive: boolean = false;
  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    this.customManagement = new CustomManagement();
    // this.customerCreation({});
  }
  changeEvent(event1: any, event2: any, event3: any) {
    console.log("activeEvent all steps");
    this.step1 = event1;
    this.step2 = event2;
    this.step3 = event3;
    setTimeout(() => {
      if (!this.isActive) {
        this.isActive = true;
        this.changeStep();
      }
    }, 100);
  }
  changeStep() {
    this.step1.disabled = true;
    this.step2.disabled = true;
    this.step3.disabled = true;

    this.step1.active = false;
    this.step2.active = false;
    this.step3.active = false;

    let activeStep: any = this.getActiveStep();
    activeStep.disabled = false;
    activeStep.active = true;
  }

  getActiveStep() {
    switch (this.currentStep) {
      case 1:
        return this.step1;
      case 2:
        return this.step2;
      case 3:
        return this.step3;
      default:
        return this.step1;
    }
  }
  contactEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 2;
    this.changeStep();
  }
  addressEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 3;
    this.changeStep();
  }

  backEvent() {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "none";
    this.currentStep = this.currentStep - 1;
    this.changeStep();
  }
  customerCreation($event: any) {
    this.customManagement.companyId = this.appService.getBranch().id;
    
    console.log(this.customManagement);
    this.restService.customerSave(this.customManagement).subscribe(result => {
      if (result != null) {
        //this.customManagement = new CustomManagement();
        if (result.message) {
          this.appService.showMessage(result.message);
        } else {
          this.appService.showMessage("Customer saved Successfully!");
        }
        this.appService.navigate("/admin/customersearch", []);
        this.currentStep = 0;
        this.getActiveStep();
      }
    });
  }
}



