  ngOnInit() {
    this.dataSource = new StockItemDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
    Observable.fromEvent(this.filter.nativeElement, "keyup")
      .debounceTime(100)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) {
          return;
        }
        this.dataSource.filter = this.filter.nativeElement.value;
      });
  }
  // callDataSourceFilter() {
