  view(item: any) {
    this.appService.navigate("/admin/stockitem-management", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }

  edit(item: any) {
    this.appService.navigate("/admin/stockitem-management", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  //For delete Popup

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogDeleteStockItem, {
      width: "500px",
      height: "250px",
      data: id
    });
    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }
  public clearAll() {
    this.searchObj = {};
    this.dataSource = new StockItemDataSource(
      this.restService,
      {},
      this.sort,
      this.paginator
    );
  }

  // public clearAll() {
  //   this.searchObj = {};
