import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from './../../../common/rest.service';
import { Prop } from './../../../common/props';

@Component({
  selector: 'app-customer-sidenav',
  templateUrl: './customer.sidenav.component.html'
 
})
export class CustomerSideNavComponent {
    mode: string;
    dataList: any[] = [];
  @Output() changeCustomer: EventEmitter<any> = new EventEmitter<any>();
constructor(private appService: AppService,private restService : RestService) {
  this.retriveData();
}
  retriveData() {
    this.restService.retriveCustomer()
      .subscribe( results => {
        this.dataList = results;
      })
  }

  onSelect(item: any) {
    this.changeCustomer.emit(item.id);
  } 

}


