import { CustomManagement } from "../../../entities/custommanagement";
import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";

@Component({
  selector: "app-customer-details",
  templateUrl: "./customer.details.component.html"
})
export class CustomerDetailsComponent {
  myForm: any;
  selectedIndex: number = 1;
  currentTab: String = "Customer Details";
  customerManagement: CustomManagement = new CustomManagement();
  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    let id = this.appService.getParam("id");
    this.customerEdit(id);
  }
  customerEdit(id) {
    this.restService.customerEdit(id).subscribe(result => {
      if (result) {
        this.customerManagement = result;
      }
    });
  }

  changeCustomer($event) {
    this.customerEdit($event);
  }
  tabChange(event: any) {
    this.currentTab = event.tab.textLabel;
  }
  add() {
    this.appService.navigate("admin/custom-management", []);
  }
}



