import { Account } from "./account.entity";
import { Profile } from "./profile.entity";
import { Address } from "./address.entity";
import { CustomerPayment } from "./customerpayment.entity";
import { CustomerTax } from "./customertax.entity";

export class CustomManagement {

    constructor() {
        this.billingAddress.lane = ''
        this.billingAddress.street = ''
        this.billingAddress.zipCode = ''
        this.gstNo = ''
        this.businessName = ''
        this.displayName = ''
        this.firstName = ''
        this.lastName = ''

    }
    id: any;
    active: boolean = true;
    title: string;
    firstName: string;
    lastName: string;
    phone:number;
    mobile:number;
    companyId:number;
    accountNumber: number;
    gstRegNumber: number;
    displayName: string;
    businessName:string;
    email: any;
    fax: number;
    webSite: any;
    others:any;
    isShippingAddress:boolean;
    shippingAddress: Address = new Address();
    billingAddress: Address = new Address();
    customerType : string;
    gstNo : string;
    paymentMethod : string;

}



