import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "../../../../common/rest.service";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import { MdSort, MdPaginator } from "@angular/material";
import { FormBuilder, FormGroup, FormControl } from "@angular/forms";

export class CRDNDataSource extends DataSource<any> {
  private data: any[] = null;
  private originaldata: any[] = [];

  constructor(
    private restService: RestService,
    private searchObj: any,
    private sort: MdSort,
    private paginator: MdPaginator,
    private crnForm: FormControl
  ) {
    super();
  }

  // filterFunction() : Observable<string> {
  //   return new Observable(observer);
  // }

  getDataList() {
    //  console.log(this.searchObj);
    this.restService.showLoader(true);
    this.restService.searchCRDNList(this.searchObj).subscribe(results => {
      if (results) {
        this.data = results;
        this.originaldata = results;
        this.restService.showLoader(false);
        console.log(this.data);
      }
    });
  }
  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connected: boolean = false;
  connect(): any {
    const displayDataChanges = [
      this.restService.searchCRDNList(this.searchObj),
      this.sort.mdSortChange,
      this.paginator.page,
      this.crnForm.valueChanges
    ];
    this.connected = true;
    console.log("im in connect yay !! ");

    return Observable.merge(...displayDataChanges).map(results => {
      if (this.data == null) {
        this.data = results;
        this.originaldata = results;
      }
      //return this.getSortedData();
      //const myData = this.data.slice();
      if (this.searchObj.crdnNumber != null) {
        if (this.searchObj.crdnNumber.length == 0) {
          this.data = this.originaldata;
        } else {
          let dataf = this.originaldata.filter(item => {
            console.log("item is", item);
            console.log("item.crdnNumber", item.crdnNumber);
            console.log(
              "compare",
              item.crdnNumber.includes(this.searchObj.crdnNumber)
            );
            return item.crdnNumber.includes(this.searchObj.crdnNumber);
          });
          //console.log('after filter ', dataf)
          this.data = dataf;
          console.log("data is now ", this.data);
        }
      }
      let myData = this.getSortedData();
      // Grab the page's slice of data.
      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      // new code

      // end new code
      return myData.splice(startIndex, this.paginator.pageSize);
    });
    // return Promise.resolve([])
  }

  disconnect() {}

  getSortedData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";
      switch (this.sort.active) {
        case "crdnNumber":
          [propertyA, propertyB] = [a.crdnNumber, b.crdnNumber];
          break;
        case "documentType":
          [propertyA, propertyB] = [a.documentType, b.documentType];
          break;
        case "receiverName":
          [propertyA, propertyB] = [a.receiverName, b.receiverName];
          break;
        case "refundVoucherValue":
          [propertyA, propertyB] = [a.refundVoucherValue, b.refundVoucherValue];
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



