import { Component, OnInit } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";
import { MdSort, MdPaginator } from "@angular/material";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";

export class ExpenseDataSource extends DataSource<any> {
  private data: any[] = [];

  constructor(
    private restService: RestService,
    private searchObject: any,
    private sort: MdSort,
    private paginator: MdPaginator
  ) {
    super();
    this.getDataList();
  }

  getDataList() {
    console.log(this.searchObject);
    this.restService.showLoader(true);
    this.restService.searchExpense(this.searchObject).subscribe(results => {
      if (results) {
        this.data = results;
        console.log("Expense Response: " + this.data);
        this.restService.showLoader(false);
      }
    });
  }
  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): any {
    const displayDataChanges = [
      this.restService.searchExpense(this.searchObject),
      this.sort.mdSortChange,
      this.paginator.page
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      //return this.getSortedData();
      //const data = this.data.slice();
      const myData = this.getSortedData();
      //this.getSortedData();
      // Grab the page's slice of data.
      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      return myData.splice(startIndex, this.paginator.pageSize);
    });
    // return Promise.resolve([])
  }

  disconnect() {}

  getSortedData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";

      switch (this.sort.active) {
        case "payeeId.id":
          [propertyA, propertyB] = [a.payeeId.id, b.payeeId.id];
          break;
        case "payeeId.name":
          [propertyA, propertyB] = [a.payeeId.name, b.payeeId.name];
          break;
        case "billDate":
          [propertyA, propertyB] = [a.billDate, b.billDate];
          break;
        case "name":
          [propertyA, propertyB] = [
            a.supplierId.firstName,
            b.supplierId.firstName
          ];
          break;
        case "paymentMode":
          [propertyA, propertyB] = [a.paymentMode, b.paymentMode];
          break;
        case "amount":
          [propertyA, propertyB] = [a.amount, b.amount];
          break;
        case "fromDate":
          [propertyA, propertyB] = [a.fromDate, b.fromDate];
          break;
        case "toDate":
          [propertyA, propertyB] = [a.toDate, b.toDate];
          break;
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



