import { Component, OnInit, Input } from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { ReportFilter } from "app/entities/report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
declare let google: any;

@Component({
  selector: "app-payable-chart",
  templateUrl: "./payable-chart.component.html",
  styleUrls: ["./payable-chart.component.scss"]
})
export class PayableChartComponent implements OnInit {
  payablesData: any[] = [];
  payablesData1: any[] = [];
  payablesData2: any[] = [];
  reportFilter: WebReportFilter = new WebReportFilter();
  x: any;
  data: any;
  @Input()
  monthsList: any[];
  payableCurrent: any;
  payableOverdue: any;
  totalPayables: any;
  overduePercent: any;
  currentPercent: any;
  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    this.payableLoad();
  }

  ngOnInit() {
    google.charts.load("current", { packages: ["corechart", "bar"] });
    google.charts.setOnLoadCallback(this.drawTick);
    this.x = setInterval(() => {
      this.drawTick();
    }, 1000);
    // this.pauseTimeLine(this.x);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }

  drawTick = () => {
    var date = new Date();
    var prevYear = date.getFullYear() - 1;
    var year =
      prevYear +
      "-" +
      date
        .getFullYear()
        .toString()
        .substring(2);

    var data = google.visualization.arrayToDataTable([
      ["Genre", "Current", "Over Due", { role: "annotation" }],
      [year, this.currentPercent, this.overduePercent, ""]
    ]);

    var options = {
      colors: ["#009688", "#ffc107"],
      height: 40,
      legend: { position: "top" },
      isStacked: true
    };
    var chart = new google.visualization.BarChart(
      document.getElementById("color")
    );
    chart.draw(data, options);
  };

  onSelectMonth(item) {
    this.reportFilter.month = item.key;
    // console.log(item.key);
    this.payableLoad();
  }
  payableLoad() {
    this.reportFilter.report = "report=DB_PAYABLES";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        // console.log(results);
        this.payableCurrent = results[0].TotalCurrent;
        this.payableOverdue = results[0].TotalOverDue;
        this.totalPayables = results[0].TotalPaybles;
        this.currentPercent = (this.payableCurrent / this.totalPayables) * 100;
        this.overduePercent = 100 - this.currentPercent;
        if (
          this.payableCurrent == null &&
          this.payableOverdue == null &&
          this.totalPayables == null
        ) {
          this.payableCurrent = 0;
          this.payableOverdue = 0;
          this.totalPayables = 0;
          this.currentPercent = 0;
          this.overduePercent = 0;
        }
        this.drawTick();
      });
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



