import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Barcode } from "../../../entities/barcode.entity";
import { AppService } from "app/shared/service/app.service";
import { Storage } from "../../../shared/utils/storage";
import { MdDialog, MdDialogRef } from "@angular/material";
import { BarcodeGenComponent } from "../barcode-gen/barcode-gen.component";

@Component({
  selector: "app-barcode-print",
  templateUrl: "./barcode-print.component.html",
  styleUrls: ["./barcode-print.component.scss"]
})
export class BarcodePrintComponent implements OnInit {
  todolist: any;
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  barcodes: any;

  constructor(
    private appService: AppService,
    public dialog: MdDialog,
    public dialogRef: MdDialogRef<BarcodePrintComponent>
  ) {
    this.getBarcodes();
  }

  ngOnInit() {}

  getBarcodes() {
    this.barcodes = Storage.getLocalItem("barcodes");
    console.log("barcode data", this.barcodes);
  }
  print(barcode) {
    let printContents = document.getElementById("barcode").innerHTML;
    let originalContents = document.body.innerHTML;

    document.body.innerHTML = printContents;
    // this.dialogRef.close();
    window.print();
    setTimeout(function() {
      window.close();
    }, 2000);
    document.body.innerHTML = originalContents;
    // this.dialogRef.close();
  }
  cancel() {
    //let originalContents = document.body.innerHTML;
    this.dialogRef.close();
    // document.body.innerHTML = originalContents;
  }
}

