import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { SupplierAddress } from "./../../../entities/supplieraddress.entity";
import { SupplierFormGroup } from "../../../forms/supplier.form";
import { SupplierManagement } from "../../../entities/suppliermanagement";

@Component({
  selector: "app-addresses",
  templateUrl: "./sm.address.component.html"
})
export class SmAddressComponent implements OnInit {
  // address:Address;
  myForm: any = SupplierFormGroup.init();
  isSameBillingAddress: boolean = true;
  isEditable: string = "true";
  isEdit: boolean = false;
  @Input("supplierManagement") supplierManagement: SupplierManagement;
  @Output("addressesEvent") addressesEvent = new EventEmitter<string>();
  @Output("backEvent") backEvent = new EventEmitter<string>();

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    SupplierFormGroup.address(this.myForm);
    this.isEditable = this.appService.getParam("isEditable");
  }

  ngOnInit() {
    console.log("Account Address");
    this.stateLoad();
    // this.cityLoad(21);
    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrieveSupplier(id);
      this.isEdit = true;
    } else {
      this.isEdit = false;
      this.isEditable = "true";
    }
  }

  done() {
    this.appService.navigate("/admin/suppliersearch", []);
  }

  retrieveSupplier(id: string) {
    this.restService.getSupplier(id).subscribe(result => {
      if (result) {
        this.supplierManagement = result;

        this.isEdit = true;
      }
    });
  }
  next() {
    this.addressesEvent.emit();
  }
  back() {
    this.backEvent.emit();
  }

  stateList: any[] = [];
  stateListTemp: any[] = [];
  stateLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.stateList = results["stateList"];
        this.stateListTemp = results;
      });
  }

  filteredStates: any[] = [];

  filterStates(val: string) {
    console.log(val);
    this.filteredStates =
      val && val != ""
        ? this.stateList.filter(
            s => s.value.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.stateList;
  }

  //  addressChange() {
  //      if(this.isSameBillingAddress) {
  //        SupplierFormGroup.shippingAddress(this.myForm, false);
  //      } else {
  //        SupplierFormGroup.shippingAddress(this.myForm, true);
  //      }
  //  }
  //   cityList: any[] = [];
  //   cityLoad(stateId:number) {
  //     this.restService.cityload(stateId)
  //       .subscribe( results => {
  //           console.log('stateId=='+stateId);
  //         this.cityList = results;
  //       })
  //   }
}



