import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { Sales } from "../../../../entities/sales.entity";
import { RestService } from "app/common/rest.service";
import { AppService } from "app/shared/service/app.service";
import { Address } from "app/entities/address.entity";
import { CustomerPayment } from "app/entities/customerpayment.entity";
import { CustomerTax } from "app/entities/customertax.entity";

@Component({
  selector: "app-products-servrvice-toggle",
  templateUrl: "./products-servrvice-toggle.component.html",
  styleUrls: ["./products-servrvice-toggle.component.scss"]
})
export class ProductsServrviceToggleComponent implements OnInit {
  @Input() myForm: any;
  @Input() itemSalesSelected: Sales;
  @Input() showAddress: boolean = false;
  @Output() outputProductToggle: EventEmitter<any> = new EventEmitter<any>();
  @Output() outputServiceToggle: EventEmitter<any> = new EventEmitter<any>();
  @Output() addProductEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() addServiceEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() changeGstEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() clearEmitter: EventEmitter<any> = new EventEmitter<any>();
  @Output() addItemEmitter: EventEmitter<any> = new EventEmitter<any>();

  clear() {
    this.clearEmitter.emit();
  }
  add() {
    this.addItemEmitter.emit();
  }

  constructor(
    private restService: RestService,
    private appService: AppService
  ) {
    // this.itemSalesSelected.isProduct = false;
  }

  ngOnInit() {
    this.itemSalesSelected.isProduct = true;
    this.itemSalesSelected.isService = false;
    this.changeProduct(true);
    this.changeService(true);
  }
  changeProduct(item: any) {
    this.outputProductToggle.emit(item);
    this.itemSalesSelected;
  }
  changeService(item: any) {
    this.outputServiceToggle.emit(item);
    this.itemSalesSelected;
  }

  addProduct(item: any) {
    console.log("toggle add product");
    this.outputProductToggle.emit(item);
  }
  addService(item: any) {
    this.outputServiceToggle.emit(item);
  }

  changeGst(isCgst: boolean) {
    if (isCgst == true) {
      this.itemSalesSelected.isCgst = this.itemSalesSelected.isIgst;
      //this.toogleService.setgstValue(isCgst);
    } else {
      this.itemSalesSelected.isIgst = !this.itemSalesSelected.isCgst;
      //this.toogleService.setgstValue(!isCgst);
    }

    this.changeGstEmitter.emit(isCgst);
  }

  changeIGst(isIgst: boolean) {
    if (isIgst == false) {
      this.itemSalesSelected.isIgst = this.itemSalesSelected.isCgst;
    } else {
      this.itemSalesSelected.isCgst = !this.itemSalesSelected.isIgst;
    }

    this.changeGstEmitter.emit(isIgst);
  }

  save() {
    this.itemSalesSelected.status = "DRAFTSAVE";
    this.salesData();
    console.log(this.itemSalesSelected.items);
    if (this.valid()) {
      this.restService.saveInvoice(this.itemSalesSelected).subscribe(result => {
        if (result) {
          this.appService.showMessage(
            "Invoice generated successfully : " + result.invoiceNumber
          );
          this.appService.navigate("admin/invoice-search", []);
        }
      });
    }
  }
  valid() {
    let returnVal = true;
    if (
      returnVal &&
      (!this.itemSalesSelected.customer.mobile ||
        this.itemSalesSelected.customer.mobile == "0" ||
        !this.itemSalesSelected.customer.firstName)
    ) {
      returnVal = false;
      this.appService.showMessage("please select customer name and mobile");
    }
    return returnVal;
  }
  billing() {
    this.itemSalesSelected.status = "BILLING";
    this.salesData();
    console.log(this.itemSalesSelected);
    if (this.valid()) {
      this.restService.saveInvoice(this.itemSalesSelected).subscribe(result => {
        if (result) {
          this.appService.showMessage(
            "Bill save successfully : " + result.invoiceNumber
          );
          this.appService.navigate("admin/invoice-search", []);
        }
      });
    }
  }
  salesData() {
    let productItems = [];
    let serviceItems = [];

    if (this.itemSalesSelected.isProduct) {
      productItems = this.itemSalesSelected.productItems.filter(item => {
        if (item.stockItemId.id != null) {
          item.service = null;
          return item;
        }
      });
    }
    if (this.itemSalesSelected.isService) {
      serviceItems = this.itemSalesSelected.serviceItems.filter(item => {
        if (item.service) {
          if (item.service.id != null) {
            item.stockItemId.id = null;
            return item;
          }
        }
      });
    }
    this.itemSalesSelected.items = [...productItems, ...serviceItems];

    if (
      this.itemSalesSelected.items == null ||
      this.itemSalesSelected.items.length == 0
    ) {
      this.appService.showMessage(
        "Please select atleast one product or service."
      );
    }
    if (this.itemSalesSelected.customer.id == null) {
      if (!this.itemSalesSelected.customer.address) {
        this.itemSalesSelected.customer.address = new Address();
      }
      this.itemSalesSelected.customer.address.lane = this.itemSalesSelected.customer.inlineAddress;
      if (this.itemSalesSelected.isBillingAddress) {
        this.itemSalesSelected.customer.shippingAddress = this.itemSalesSelected.customer.address;
      } else {
        this.itemSalesSelected.customer.shippingAddress = this.itemSalesSelected.address;
      }
      this.itemSalesSelected.customer.customerPayment = new CustomerPayment();
      this.itemSalesSelected.customer.customerTax = new CustomerTax();
    }
    this.itemSalesSelected.userId = 1; // this.appService.getSessionUser().id;
    console.log(this.appService.getSessionUser());
  }
}



