import { Component, OnInit, ViewChild } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { Util } from "../../../shared/utils/util";
import { Storage } from "../../../shared/utils/storage";
import { Sales } from "./../../../entities/sales.entity";
import { Item } from "./../../../entities/item.entity";
import { Customer } from "../../../entities/customer.entity";
import { InvoiceFormGroup } from "../../../forms/invoice.form";
import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
import { IPageChangeEvent } from "@covalent/core";
import { DataSource } from "@angular/cdk";
import { InvoiceDataSource } from "./invoice.datasource";
import { Email } from "./../../../entities/invoiceSearchEmail.entity";
import { MdSort } from "@angular/material";
import { DialogContentExampleDialogInvoice } from "./DialogContentExampleDialogInvoice";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { DialogContentExampleDialogInvoices } from "./DialogContentExampleDialogInvoices.component";
import { Invoice80mmComponent } from "../invoice80mm/invoice80mm.component";
// import {MdDialog} from '@angular/material';

@Component({
  selector: "app-invoice-search",
  templateUrl: "./invoice.search.component.html",
  styleUrls: ["./invoice.search.component.scss"]
})
export class InvoiceSearchComponent implements OnInit {
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;
  dataSource: InvoiceDataSource = null;
  displayedColumns: any[] = [
    "invoice",
    "businessName",
    "date",
    "amount",
    "action"
  ];
  // "mobile",
  selectedItem: any;
  showSide: boolean = false;
  email: any = {};
  serachObj: any = {};

  constructor(
    private appService: AppService,
    private restService: RestService,
    private _dataTableService: TdDataTableService,
    public dialog: MdDialog
  ) {}
  ngOnInit(): void {
    // this.searchCall();
    // this.serachObj.period = "year";
    this.dataSource = new InvoiceDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
    this.periodLoad();
    this.rangeLoad();
  }
  clearAll() {
    this.serachObj = {};
    this.dataSource = new InvoiceDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
  }
  dataList: any = [];
  // searchCall(){
  //   this.searchObject.period = "year";
  //   this.restService.searchInvoice(this.searchObject)
  //     .subscribe( result => {
  //       this.dataList = result;
  //       console.log(result)
  //     })
  // }

  onSelectedItem(item: any) {
    this.selectedItem = item;
    console.log(this.selectedItem);
  }
  // edit() {
  //   this.restService.invoicePrint(this.selectedItem.id);
  // }

  print() {
    this.restService.invoicePrint(this.selectedItem.id);
  }
  open() {
    this.restService.invoiceOpen(this.selectedItem.id);
  }

  view(row) {
    this.selectedItem = row;
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }

  pop() {
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }
  // // opens() {
  // //   const dialogRef = this.dialog.open(DialogContentExampleDialogInvoices, {
  // //     width: '794px',
  // //     height: '900px',
  // //     // data: id

  // //   });

  //   dialogRef.afterClosed().subscribe(result => {
  //   this.clearAll();
  //   });
  // }

  download() {
    this.restService.invoiceDownload(this.selectedItem.id);
  }
  opens(id) {
    // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
    let companyInfo = Storage.getSessionUser();

    let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
    if (parseInt(pageLayoutId) == 1) {
      const dialogRef = this.dialog.open(DialogContentExampleDialogInvoices, {
        // width: "1500px",
        // height: "850px",
        data: id
      });
    }
    if (parseInt(pageLayoutId) == 2) {
      const dialogRef = this.dialog.open(Invoice80mmComponent, {
        // width: "279.66",
        // height: "508.92px",
        data: id
      });
    }
  }

  openDialog(id) {
    const dialogRef = this.dialog.open(DialogContentExampleDialogInvoice, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }
  delete() {
    this.openDialog(this.selectedItem.id);
  }
  onClose(item: any) {
    this.showSide = false;
  }
  emailSave() {
    this.email.from = this.appService.getSessionUser().email;
    this.email.fileName = "Invoice.pdf";
    this.email.content = btoa(this.email.message);
    delete this.email.message;

    this.restService
      .invoiceEmail(this.selectedItem.id, this.email)
      .subscribe(result => {
        this.showSide = false;
      });
  }
  sendEmail() {
    this.showSide = true;
    // if (!item) {
    let item = new Email();
    // }
    this.email = Object.assign({}, item);
    this.email.to = this.selectedItem.emailId;
  }

  periodList: any[] = [];
  periodLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.periodList = results["Period"];
      });
  }

  rangeList: any[] = [];
  rangeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.rangeList = results["Range"];
      });
  }

  callDataSource() {
    console.log(this.serachObj.fromDate);
    if (this.serachObj.fromDate && typeof this.serachObj.fromDate == "object") {
      this.serachObj.fromDate = Util.ISODate(
        this.serachObj.fromDate.toISOString()
      );
    }
    if (this.serachObj.toDate && typeof this.serachObj.toDate == "object") {
      this.serachObj.toDate = Util.ISODate(this.serachObj.toDate.toISOString());
    }
    this.dataSource = new InvoiceDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
  }
  edit() {
    console.log(
      "edit function called" + this.selectedItem.customer.businessName
    );
    Storage.setLocalItem("selectedItem", this.selectedItem);
    this.appService.navigate("/invoice/add/", [{ isEditable: true }]);
  }
}



