import { ChartofAccount } from "./chartofaccount.entity";
import { SupplierManagement } from "./suppliermanagement";
import { ExpenseAccountDetails } from "./expenseaccountdetails.entity";
import { ExpensePayee } from "./expensepayee.entity";

export class ExpenseManagement {
  id: any;
  paymentMode: string;
  referenceNo: string;
  chequeNo: string;
  paymentDate: Date;
  billDate: Date;
  chequeDate: Date;
  bankName: string;
  others: string;
  amount: number;
  deleted: number;
  isSupplier: boolean;
  expenseName: string;
  expenseAccountList: ExpenseAccountDetails;
  expenseAccountDetails: ExpenseAccountDetails[] = [];
  creditAccountId: ChartofAccount = new ChartofAccount();
  payeeId: ExpensePayee = new ExpensePayee();

  subTotal: number = 0;
  totalTax: number = 0;
  grandTotal: number = 0;
}



