import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from './../../../common/rest.service';
import { Prop } from './../../../common/props';
import { InvoiceFormGroup } from '../../../forms/invoice.form';
import { CustomManagement } from '../../../entities/custommanagement';
import { CustomerFormGroup } from '../../../forms/customer.form';
@Component({
    selector: 'app-billing',
    templateUrl: './cm.billing.component.html'

})
export class CmBillingComponent implements OnInit {
    customerManagement: CustomManagement;
    customerType = ["Retail", "Credit"];
    paymentMethod = ["Cash","Electronic","Cheque"];
    myForm: any = CustomerFormGroup.init();
    @Input('customManagement')
    customManagement: CustomManagement;

    @Output('billingEvent')
    billingEvent = new EventEmitter<string>();

    @Output('backEvent')
    backEvent = new EventEmitter<string>();
    constructor(private appService: AppService, private restService: RestService) {
        CustomerFormGroup.taxGroup(this.myForm);
    }

    ngOnInit() {
      
     }
    next() {
        this.billingEvent.emit()
    }
    back() {
        this.backEvent.emit();
    }

    paymentTypeList: any[] = [];
    paymentTypeLoad() {
        this.restService.dataload('Payment')
            .subscribe(results => {
                this.paymentTypeList = results;
            })
    }


}


