import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
  OnChanges
} from "@angular/core";

import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { InvoiceFormGroup } from "../../../forms/invoice.form";
import { CustomManagement } from "../../../entities/custommanagement";
import { CustommanagementFormGroup } from "../../../forms/custommanagement.form";
import { TranscationDataSource } from "./transcation.datasource";
import { Storage } from "../../../shared/utils/storage";
import { DataSource } from "@angular/cdk";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import { MdSort } from "@angular/material";
import { MdPaginator, MdDialog } from "@angular/material";
import { DialogComponent } from "./dialog.component";
@Component({
  selector: "app-transcation",
  templateUrl: "./transcation.component.html"
})
export class TranscationComponent implements OnInit, OnChanges {
  customerManagement: CustomManagement;
  selectedItemForView: any = null;
  dataSource: TranscationDataSource = null;
  displayedColumns: any[] = [
    "date",
    "no",
    "Amount",
    "status",
    "receivedAmount",
    "receivables",
    "dueDate",
    "overdueReceivables",
    "overdueTime",
    "action"
  ];
  selectedItem: any;
  serachObj: any = {};
  overDueTime: any;
  @ViewChild("filter") filter: ElementRef;
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;

  @Input() customerId: string;
  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {
    //this.dataSource = new TranscationDataSource(this.restService, this.searchObject);
  }

  ngOnInit(): void {
    this.serachObj.period = "year";
    this.dataSource = new TranscationDataSource(
      this.restService,
      this.serachObj,
      this.sort,
      this.paginator
    );
    Observable.fromEvent(this.filter.nativeElement, "keyup")
      .debounceTime(100)
      .distinctUntilChanged()
      .subscribe(() => {
        if (!this.dataSource) {
          return;
        }
        this.dataSource.filter = this.filter.nativeElement.value;
      });
    this.customerId = this.appService.getParam("id");

    console.log(this.customerId);
    console.log("transaction");
    this.transactionList();
  }

  ngOnChanges(changes: any) {
    console.log(changes.customerId);
    // if(changes.customerId && changes.customerId.currentValue ) {
    //       console.log("customer Id: "+ this.customerId);
    //      this.searchObject = {"customerId": this.customerId}
    //      this.dataSource = new TranscationDataSource(this.restService, this.searchObject);
    // }
  }
  searchObject: any = { customerId: this.customerId };
  dataList: any[] = [];

  transactionList() {
    this.searchObject = { customerId: this.customerId };
    this.dataSource = new TranscationDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }
  onSelectedItem(item: any) {
    this.selectedItem = item;
    console.log(this.selectedItem);
  }
  print() {
    this.restService.invoicePrint(this.selectedItem.id);
  }
  open() {
    this.restService.invoiceOpen(this.selectedItem.id);
  }
  download() {
    this.restService.invoiceDownload(this.selectedItem.id);
  }
  delete() {
    this.restService
      .invoiceDelete({ id: [this.selectedItem.id] })
      .subscribe(result => {
        this.dataSource = new TranscationDataSource(
          this.restService,
          this.searchObject,
          this.sort,
          this.paginator
        );
      });
  }
  view(row) {
    // let day = new Date().getDate();
    this.selectedItem = row;
    this.overDueTime = new Date().getDate();
    console.log(row.overdueTime);
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }

  pop() {
    Storage.setLocalItem("selectedItem", this.selectedItem);
    setTimeout(() => {
      this.opens(null);
    }, 10);
  }
  opens(id) {
    // let pageLayoutId : string = Storage.getLocalItem('pageLayoutId');
    let companyInfo = Storage.getSessionUser();

    let pageLayoutId = companyInfo.company.settingPayload.printLayoutId;
    if (parseInt(pageLayoutId) == 1) {
      const dialogRef = this.dialog.open(DialogComponent, {
        // width: "1500px",
        // height: "850px",
        data: id
      });
    }
  }
  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/customer-details", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);
  }
}



