import {
  Component,
  Inject,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { MdDialog, MdDialogRef, MD_DIALOG_DATA } from "@angular/material";
import { RestService } from "../../../common/rest.service";
import { AppService } from "../../../shared/service/app.service";
import { Storage } from "../../../shared/utils/storage";

import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
import { Sales } from "app/entities/sales.entity";
import { DataSource } from "@angular/cdk";

@Component({
  selector: "app-invoice80mm",
  templateUrl: "./invoice80mm.component.html",
  styleUrls: ["./invoice80mm.component.scss"]
})
export class Invoice80mmComponent {
  id: any;
  sales: any;
  // akhdfbv:""jhdfb";
  dataSource: any;
  companyInfo: any;
  bankDetails: any;
  stockItemDataSource: any[] = [];
  servicesDataSource: any;
  selectedItem: any;
  displayedColumns: any[] = [
    "Item",
    "HSN",
    "Qty",
    "Rate",
    "Discount",
    "Taxable Value",
    "SGST",
    "CGST",
    "IGST",
    "CESS",
    "Total"
  ];
  displayedColumnsDef: any[] = [
    "stockItemId.productName",
    "stockItemId.hsnCodeNames",
    "quantity",
    "unit",
    "discountAmount",
    "taxAmount",
    "sgstAmount",
    "csgstAmount",
    "IgstAmount",
    "cessAmount",
    "totalAfterTax"
  ];
  constructor(
    private restService: RestService,
    private appService: AppService,
    public dialogRef: MdDialogRef<Invoice80mmComponent>,
    @Inject(MD_DIALOG_DATA) public data: any
  ) {
    this.companyInfo = Storage.getSessionUser().company;
    console.log(this.companyInfo);
    this.dataSource = Storage.getLocalItem("selectedItem");
    console.log(this.dataSource);
    this.stockItemDataSource = this.dataSource.productItems;
    this.servicesDataSource = this.dataSource.serviceItems;
    console.log(" this.stockItemDataSource ", this.stockItemDataSource);
    this.bankDetails = Storage.getSessionUser().bankDetails;
    console.log("this.bankDetails", this.bankDetails);
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  ok() {
    this.restService.invoiceDelete(this.id).subscribe(result => {
      this.dialogRef.close();
      this.appService.showMessage("Invoice Deleted Successfully ");
    });
  }

  print() {
    const printContent = document.getElementById("component1");

    const WindowPrt = window.open(
      "",
      "",
      "left=0,top=0,width=900,height=900,toolbar=0,scrollbars=0,status=0"
    );
    WindowPrt.document.write(printContent.innerHTML);
    WindowPrt.document.close();
    WindowPrt.focus();
    WindowPrt.print();
    WindowPrt.close();
  }
  cancel() {
    this.dialogRef.close();
  }
}



