import { FormGroup } from "@angular/forms";
import { AppFormGroup } from "./../shared/common/app.form";

export class UserSettingFormGroup extends AppFormGroup {
  static init() {
    return this.getFormGroup();
  }

  static edit(group: FormGroup) {
    //============ Company Information fields ============
    group.addControl(
      "us_company_name",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_gstin",
      this.getController(false, this.GST_CODE_PATTERN, 15, 15)
    );
    group.addControl(
      "us_company_logo",
      this.getController(false, this.TYPE_ALL, null, 45)
    );
    group.addControl(
      "us_company_website",
      this.getController(false, this.WEBSITE_PATTERN, null, 45)
    );
    group.addControl(
      "us_company_address_lane",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_address_street",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_address_area",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_address_city",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_address_state",
      this.getController(false, this.TYPE_ALL, null, 50)
    );
    group.addControl(
      "us_company_address_zipcode",
      this.getController(false, this.TYPE_ZIP_CODE, null, 50)
    );

    //=============== User profile fields =================
    group.addControl(
      "us_user_firstname",
      this.getController(false, this.TYPE_ALFA, null, 50)
    );
    group.addControl(
      "us_user_lastname",
      this.getController(false, this.TYPE_ALFA, null, 50)
    );
    group.addControl(
      "us_user_mobile",
      this.getController(null, this.TYPE_MOBILE_NO, 10, 10)
    );
    group.addControl(
      "us_user_pan",
      this.getController(false, this.PAN_NO_PATTERN, null, 20)
    );

    //=============== Bank Information fields ===============
    group.addControl(
      "us_bank_name",
      this.getController(false, this.TYPE_ALFA_NUM_DES_SPACE_HIFEN, null, 300)
    );
    group.addControl(
      "us_bank_accountnumber",
      this.getController(false, this.TYPE_NUMBER, null, 50)
    );
    group.addControl(
      "us_bank_ifsc",
      this.getController(false, this.TYPE_IFSC_CODE, null, 50)
    );
    group.addControl(
      "us_bank_branch",
      this.getController(false, this.TYPE_ALFA_NUM_DES_SPACE_HIFEN, null, 20)
    );
    group.addControl(
      "us_bank_address",
      this.getController(false, this.TYPE_ALL, null, 500)
    );

    //=============== Invoice Information ==================
    group.addControl(
      "us_invoice_footer",
      this.getController(false, this.TYPE_ALL, null, 150)
    );
    group.addControl(
      "us_quotation_footer",
      this.getController(false, this.TYPE_ALL, null, 150)
    );
    group.addControl(
      "us_layout_size",
      this.getController(false, this.TYPE_ALL, null, 10)
    );
    group.addControl(
      "us_is_product",
      this.getController(false, this.TYPE_ALL, null, 10)
    );
    group.addControl(
      "us_is_service",
      this.getController(false, this.TYPE_ALL, null, 10)
    );

    //=============== Staff Information ==================
    group.addControl(
      "staff_firstName",
      this.getController(null, this.TYPE_ALFA, null, 50)
    );
    group.addControl(
      "staff_lastName",
      this.getController(null, this.TYPE_ALFA, null, 20)
    );
    group.addControl(
      "staff_mobile",
      this.getController(null, this.TYPE_MOBILE_NO, 10, 10)
    );
    group.addControl(
      "staff_password",
      this.getController(null, this.TYPE_ALFA, 10, 10)
    );
  }
}



