import { Component, OnInit, Input } from "@angular/core";
import { AppService } from "../../../../shared/service/app.service";
import { AnimationService } from "../../../../shared/service/animation.service";
import { RestService } from "./../../../../common/rest.service";
import { Prop } from "./../../../../common/props";
import { WebReportFilter } from "app/entities/web-report-filter.entity";
import { BaseReportComponent } from "app/shared/component/base.report.component";
declare let google: any;

@Component({
  selector: "app-sales-chart",
  templateUrl: "./sales-chart.component.html",
  styleUrls: ["./sales-chart.component.scss"]
})
export class SalesChartComponent implements OnInit {
  salesHead: any[] = [];
  salesData: any[];
  x: any;
  data: any;
  @Input()
  yearsList: any[];
  salesMonth: any;
  monthly: any[];
  finalData: any[] = [];
  totalSales: any;
  reportFilter: WebReportFilter = new WebReportFilter();
  constructor(
    private appService: AppService,
    private restService: RestService,
    private basReportOp: BaseReportComponent
  ) {
    // this.lineDataLoad();
    this.salesLoad();
  }

  ngOnInit() {
    google.charts.load("current", { packages: ["corechart", "line"] });
    google.charts.setOnLoadCallback(this.drawBasic);

    this.x = setInterval(() => {
      this.drawBasic();
    }, 1000);
    // this.pauseTimeLine(this.x);
    setTimeout(() => {
      this.pauseTimeLine(this.x);
    }, 3000);
  }

  drawBasic = () => {
    var data = new google.visualization.DataTable();
    data.addColumn("string", "Months");
    data.addColumn("number", "sales");
    data.addRows(this.finalData);

    var options = {
      colors: ["#58bc82"],
      vAxis: {}
    };
    var chart = new google.visualization.LineChart(
      document.getElementById("chart2")
    );

    chart.draw(data, options);
  };
  onSelectYear(item) {
    this.reportFilter.year = item;
    // console.log(item);
    this.salesLoad();
  }
  salesLoad() {
    this.finalData = [];
    this.reportFilter.report = "report=DB_SALES";
    this.data = this.basReportOp
      .getDS(this.reportFilter)
      .subscribe((results: any) => {
        // console.log(results);
        this.salesData = results;
        this.monthSort();
        // console.log(this.monthly);
        this.monthly.map(x => {
          var xArray: any[] = [x.SalesMonthName, x.TotalSales];
          // console.log("xArray :: " + xArray);
          this.finalData.push(xArray);
        });
        this.drawBasic();
      });
  }
  monthSort() {
    var allMonths = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December"
    ];

    this.monthly = this.salesData.sort((a, b) => {
      return (
        allMonths.indexOf(a.SalesMonthName) -
        allMonths.indexOf(b.SalesMonthName)
      );
    });
    // console.log(this.monthly);
  }

  pauseTimeLine(x) {
    clearInterval(this.x);
  }
}



