import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { Util } from "../../../shared/utils/util";
import { InvoiceFormGroup } from "../../../forms/invoice.form";
import { SupplierManagement } from "../../../entities/suppliermanagement";
import { SupplierFormGroup } from "../../../forms/supplier.form";
import { SupplierDataSource } from "./supplier.datasource";
import { ApexService } from "../../../shared/service/apex.service";
import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
import { DataSource } from "@angular/cdk";
//import { MdSort } from '@angular/material';
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/fromEvent";
import "rxjs/add/operator/debounceTime";
import "rxjs/add/operator/distinctUntilChanged";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import { Email } from "../../../entities/supplierSearchEmail.entity";
import { DialogContentExampleDialog } from "./DialogContentExampleDialog";
import {
  MdDialog,
  MdSort,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
//import { AngularFixedModule }        from 'angular-fixed-table-header';

@Component({
  selector: "app-supplier-search",
  templateUrl: "./supplier.search.component.html",
  styleUrls: ["./supplier.search.component.scss"]
})
export class SupplierSearchComponent implements OnInit {
  dataSource: SupplierDataSource = null;
  //dataSource: SupplierDataSource = null
  //displayedColumns: any[] = ["name", "company", "email", "street","state","city","phone","balance","action"];
  displayedColumns: any[] = [
    "company",
    "state",
    "city",
    "mobileNumber",
    "action"
  ];
  selectedItem: any = null;
  showSide: boolean = false;
  searchObject: any = {};
  email: any = {};
  selectedItemForView: any = null;
  filteredStates: any[] = [];
  //@ViewChild('filter') filter: ElementRef;
  //searchObj: any = {};
  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;

  constructor(
    private appService: AppService,
    private restService: RestService,
    private apexService: ApexService,
    private _dataTableService: TdDataTableService,
    public dialog: MdDialog
  ) {}
  openDialog(id) {
    const dialogRef = this.dialog.open(DialogContentExampleDialog, {
      width: "500px",
      height: "250px",
      data: id
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  public clearAll() {
    this.searchObject = {};
    this.dataSource = new SupplierDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }
  ngOnInit(): void {
    this.dataSource = new SupplierDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
    this.periodLoad();
    this.rangeLoad();
    this.stateLoad();
  }

  periodList: any[] = [];
  periodLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.periodList = results["Period"];
      });
  }

  rangeList: any[] = [];
  rangeLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.rangeList = results["Range"];
      });
  }

  stateLoad() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.filteredStates = results["stateList"];
      });
  }

  //     @Output() confirm = new EventEmitter<any>();
  //
  //   constructor(private el: ElementRef) {
  //   }
  //
  //   @HostListener('click')
  //    onClick() {
  //      $.confirm({
  //      buttons: {
  //     confirm: () => this.confirm.emit()
  //   }
  //  });
  // }

  dataList: any = [];

  edit() {
    this.appService.navigate("./admin/supplier-management", [
      { id: this.selectedItem.id, isEditable: true }
    ]);
  }

  view() {
    this.appService.navigate("./admin/supplier-management", [
      { id: this.selectedItem.id, isEditable: false }
    ]);
  }
  delete() {
    // this.restService.supplierDelete({ id: [this.selectedItem.id] })
    //   .subscribe(result => {

    //     this.apexService.showMessages("supplier details deleted successfully.", 1500);
    //     this.dataSource = new SupplierDataSource(this.restService, this.searchObject, this.sort);
    //   });
    this.openDialog({ id: [this.selectedItem.id] });
    // this.dataSource = new SupplierDataSource(this.restService, this.searchObject, this.sort);
  }

  onSelectedItem(row) {
    this.selectedItem = row;
  }

  callDataSource() {
    if (
      this.searchObject.fromDate &&
      typeof this.searchObject.fromDate == "object"
    ) {
      this.searchObject.fromDate = Util.ISODate(
        this.searchObject.fromDate.toISOString()
      );
    }
    if (
      this.searchObject.toDate &&
      typeof this.searchObject.toDate == "object"
    ) {
      this.searchObject.toDate = Util.ISODate(
        this.searchObject.toDate.toISOString()
      );
    }
    this.dataSource = new SupplierDataSource(
      this.restService,
      this.searchObject,
      this.sort,
      this.paginator
    );
  }

  print() {
    //    this.restService.paymentPrintPDF(this.selectedItem.id);
    this.restService.supplierPrint(this.selectedItem.id);
  }
  open() {
    this.restService.supplierOpen(this.selectedItem.id);
  }
  download() {
    this.restService.supplierDownload(this.selectedItem.id);
  }

  sendEmail() {
    this.showSide = true;
    // if (!item) {
    //  alert(this.selectedItem.emailId);
    let item = new Email();
    // }
    this.email = Object.assign({}, item);
    this.email.to = this.selectedItem.emailId;
  }
  onClose(item: any) {
    this.showSide = false;
  }
  emailSave() {
    //this.email.to = this.selectedItem.email;
    this.email.from = this.appService.getSessionUser().email;
    this.email.fileName = "Supplier.pdf";
    this.email.content = btoa(this.email.message);
    delete this.email.message;

    this.restService
      .supplierEmail(this.selectedItem.id, this.email)
      .subscribe(result => {
        this.showSide = false;
      });
  }
  myFun(row) {
    this.selectedItemForView = row;
    this.appService.navigate("./admin/supplier-management", [
      { id: this.selectedItemForView.id, isEditable: false }
    ]);
  }
}



