import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import { MdSort, MdPaginator } from "@angular/material";
import { RestService } from "app/common/rest.service";
import { FormBuilder, FormGroup, FormControl } from "@angular/forms";

export class StockItemDataSource extends DataSource<any> {
  // public data: any[] = [];
  // private data: any[] = null;
  // private originaldata: any[] = null;
  _filterChange = new BehaviorSubject("");

  public get filter(): string {
    return this._filterChange.value;
  }
  public set filter(filter: string) {
    this._filterChange.next(filter);
  }
  public data: any[] = [];
  constructor(
    private restService: RestService,
    private searchObj: any,
    private sort: MdSort,
    private paginator: MdPaginator //   private stockForm: FormControl
  ) {
    super();
    this.restService.searchStockItems(this.searchObj).subscribe(results => {
      if (results) {
        this.data = results;
      }
    });

    // this.getDataList();
  }

  // getDataList() {
  //   this.restService.showLoader(true);
  //   this.restService.searchStockItems(this.searchObj).subscribe(results => {
  //     if (results) {
  //       this.data = results;
  //       this.originaldata = results;
  //       this.restService.showLoader(false);
  //       console.log(this.data);
  //     }
  //   });
  // }
  // connected: boolean = false;
  connect(): any {
    const displayDataChanges = [
      this.restService.searchStockItems(this.searchObj),
      this._filterChange,
      this.sort.mdSortChange,
      this.paginator.page
      //   this.stockForm.valueChanges
    ];
    // this.connected = true;
    console.log("im in connect yay !! ");

    return Observable.merge(...displayDataChanges).map(() => {
      this.data = this.data.slice().filter((item: any) => {
        // return this.data.slice().filter((item: any) => {
        //console.log(item)
        let searchStr: string =
          item.productName +
          "" +
          item.stockathand +
          "" +
          item.categoryName +
          "" +
          item.brandName +
          "" +
          item.companyId;
        //console.log(searchStr);
        return searchStr.toLowerCase().indexOf(this.filter.toLowerCase()) != -1;
      });

      const myData = this.getSortedData();

      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      return myData.splice(startIndex, this.paginator.pageSize);
    });

    // return Observable.merge(...displayDataChanges).map(results => {
    //   if (this.data == null) {
    //     this.data = results;
    //     this.originaldata = results;
    //   }
    //   if (this.searchObj.productName != null) {
    //     if (this.searchObj.productName.length == 0) {
    //       this.data = this.originaldata;
    //     } else {
    //       let dataf = this.originaldata.filter(item => {
    //         console.log("item is", item);
    //         console.log("item.productName", item.productName);
    //         console.log(
    //           "compare",
    //           item.productName.includes(this.searchObj.productName)
    //         );
    //         return item.productName.includes(this.searchObj.productName);
    //       });
    //       console.log("after filter ", dataf);
    //       this.data = dataf;
    //       console.log("data is now ", this.data);
    //     }
    //   }

    //   let myData = this.getSortedData();

    //   // Grab the page's slice of data.
    //   const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
    //   return myData.splice(startIndex, this.paginator.pageSize);
    // });
  }

  disconnect() {}

  getSortedData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";
      switch (this.sort.active) {
        case "productName":
          [propertyA, propertyB] = [a.productName, b.productName];
          break;
        case "stockathand":
          [propertyA, propertyB] = [
            a.stockItemPrice.stockathand,
            b.stockItemPrice.stockathand
          ];
          break;
        case "categoryName":
          [propertyA, propertyB] = [a.categoryName, b.categoryName];
          break;
        case "brandName":
          [propertyA, propertyB] = [a.brandName, b.brandName];
          break;
        case "mrpPrice":
          [propertyA, propertyB] = [a.stockItemPrice.mrp, b.stockItemPrice.mrp];
          break;
        case "costPrice":
          [propertyA, propertyB] = [
            a.stockItemPrice.itemCostPrice,
            b.stockItemPrice.itemCostPrice
          ];
          break;
        case "sellingPrice":
          [propertyA, propertyB] = [
            a.stockItemPrice.sellingPrice,
            b.stockItemPrice.sellingPrice
          ];
          break;
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



