import { CustomManagement } from "../../../entities/custommanagement";
import {
  Component,
  OnInit,
  ViewChild,
  Output,
  EventEmitter
} from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { Prop } from "../../../common/props";
import { ProductDataSource } from "./product.search.datasource";

import { MdSort } from "@angular/material";
import { DataSource } from "@angular/cdk";
//import { DialogContentExampleDialogService } from "./DialogContentExampleDialogService";
import { DialogContentExampleDialogInventory } from "./DialogContentExampleDialogInventory";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";

@Component({
  selector: "app-product-search",
  templateUrl: "./product.search.component.html"
})
export class ProductSearchComponent {
  // activeTab: string ="search";
  // inventoryId: string;
  // nonInventoryId: string;
  // serviceId: string;

  @ViewChild(MdSort) sort: MdSort;
  @ViewChild(MdPaginator) paginator: MdPaginator;
  //@Output() updateProduct = new EventEmitter();
  dataSource: ProductDataSource = null;
  displayedColumns: any[] = [
    "product",
    "typeofproduct",
    "qty",
    "lowstockalert",
    "action"
  ];
  selectedItem: any;
  serviceTypeList: string;
  searchObj: any = {};
  selectedItemForView: any = null;

  constructor(
    private appService: AppService,
    private restService: RestService,
    public dialog: MdDialog
  ) {
    this.searchObj.type = "All";
    let id = this.appService.getBranch();
    this.searchProduct();
  }
  //For delete Popup
  openDialog(id, type) {
    let myData = { id: id, type: type };
    const dialogRef = this.dialog.open(DialogContentExampleDialogInventory, {
      width: "500px",
      height: "250px",
      data: myData
    });

    dialogRef.afterClosed().subscribe(result => {
      this.clearAll();
    });
  }

  ngOnInit(): void {
    this.typeList();
    this.callDataSource();
  }
  callDataSource() {
    this.dataSource = new ProductDataSource(
      this.restService,
      this.searchObj,
      this.sort,
      this.paginator
    );
  }

  clearAll() {
    this.searchObj = {};
    this.searchObj.type = "All";
    this.callDataSource();
  }

  clear() {
    // this.searchObj = {};
    // this.searchObj.type = "Inventory";
    // this.callDataSource();
  }

  searchProduct() {}
  selectItem(item: any) {
    this.selectedItem = item;
  }
  private typeList() {
    this.restService.dataload("/ServiceType").subscribe(results => {
      if (results) {
        this.serviceTypeList = results;
      }
    });
  }
  // edit() {
  //     let emitObj: any = {}
  //     emitObj.id = this.selectedItem.id;
  //     emitObj.type = this.searchObj.type;
  //     this.updateProduct.emit(emitObj);

  // }

  edit() {
    // console.log(this.selectedItem)
    this.searchObj = this.selectedItem;
    if (this.searchObj.productType == "Inventory") {
      this.appService.navigate("./admin/inventory-edit", [
        { id: this.selectedItem.id, isEditable: true }
      ]);
    } else if (this.searchObj.productType == "NonInventory") {
      this.appService.navigate("./admin/noninventory-edit", [
        { id: this.selectedItem.id, isEditable: true }
      ]);
    } else {
      this.appService.navigate("./admin/service-edit", [
        { id: this.selectedItem.id, isEditable: true }
      ]);
    }
  }

  view() {
    // console.log(this.selectedItem)
    this.searchObj = this.selectedItem;
    if (this.searchObj.productType == "Inventory") {
      this.appService.navigate("./admin/inventory-edit", [
        { id: this.selectedItem.id, isEditable: false }
      ]);
    } else if (this.searchObj.productType == "NonInventory") {
      this.appService.navigate("./admin/noninventory-edit", [
        { id: this.selectedItem.id, isEditable: false }
      ]);
    } else {
      this.appService.navigate("./admin/service-edit", [
        { id: this.selectedItem.id, isEditable: false }
      ]);
    }
  }

  delete() {
    // console.log(this.selectedItem)
    this.searchObj = this.selectedItem;
    this.openDialog(this.selectedItem.id, this.searchObj.type);
  }

  // openDialog(id) {
  //     if(this.searchObj.type =="Service") {
  //         const dialogRef = this.dialog.open(DialogContentExampleDialogService, {
  //             width: '500px',
  //             height: '250px',
  //             data: id  });

  //               dialogRef.afterClosed().subscribe(result => {
  //               this.clearAll();
  //               });
  //     }else{
  //         const dialogRef = this.dialog.open(DialogContentExampleDialogInventory, {
  //             width: '500px',
  //             height: '250px',
  //             data: id  });

  //               dialogRef.afterClosed().subscribe(result => {
  //               this.clearAll();
  //               });
  //     }

  myFun(row) {
    this.selectedItemForView = row;
    console.log(row);
    console.log(this.searchObj);
    if (this.selectedItemForView.productType == "Inventory") {
      this.appService.navigate("./admin/inventory-edit", [
        { id: this.selectedItemForView.id, isEditable: false }
      ]);
    } else if (this.selectedItemForView.productType == "NonInventory") {
      this.appService.navigate("./admin/noninventory-edit", [
        { id: this.selectedItemForView.id, isEditable: false }
      ]);
    } else {
      this.appService.navigate("./admin/service-edit", [
        { id: this.selectedItemForView.id, isEditable: false }
      ]);
    }
  }
}



