import { Component, OnInit, ViewChild } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { MdSort, MdPaginator } from "@angular/material";
import { Observable } from "rxjs/Observable";
import "rxjs/add/observable/merge";

export class InvoiceDataSource extends DataSource<any> {
  public data: any[] = [];
  renderedData: any[] = [];
  constructor(
    private restService: RestService,
    private searchObj: any,
    private sort: MdSort,
    private paginator: MdPaginator
  ) {
    super();
    this.getDataList();
  }

  getDataList() {
    console.log(this.searchObj);
    this.restService.showLoader(true);
    this.restService.searchInvoice(this.searchObj).subscribe(results => {
      if (results) {
        this.data = results;
        this.restService.showLoader(false);
        console.log(this.data);
      }
    });
  }
  /** Connect function called by the table to retrieve aone stream containing the data to render. */
  connect(): any {
    const displayDataChanges = [
      this.restService.searchInvoice(this.searchObj),
      this.sort.mdSortChange,
      this.paginator.page
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      //return this.getSortedData();
      //const myData = this.data.slice();
      const myData = this.getSortedData();

      // Grab the page's slice of data.
      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      return myData.splice(startIndex, this.paginator.pageSize);
      //return this.getSortedData();
    });
    // return Promise.resolve([])
  }

  disconnect() {}

  getSortedData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";
      switch (this.sort.active) {
        case "date":
          [propertyA, propertyB] = [a.invoiceDate, b.invoiceDate];
          break;
        case "invoice":
          [propertyA, propertyB] = [a.invoiceNumber, b.invoiceNumber];
          break;
        case "businessName":
          [propertyA, propertyB] = [
            a.customer.businessName,
            b.customer.businessName
          ];
          break;
        case "mobile":
          [propertyA, propertyB] = [a.customer.mobile, b.customer.mobile];
          break;
        case "amount":
          [propertyA, propertyB] = [a.total, b.total];
          break;
        case "invoicetype":
          [propertyA, propertyB] = [a.invoiceType, b.invoiceType];
          break;
        case "fromDate":
          [propertyA, propertyB] = [a.fromDate, b.fromDate];
          break;
        case "toDate":
          [propertyA, propertyB] = [a.toDate, b.toDate];
          break;
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



