import { Component, OnInit } from "@angular/core";
import { DataSource } from "@angular/cdk";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/observable/merge";
import "rxjs/add/operator/map";
import { MdSort, MdPaginator } from "@angular/material";

export class CustomerDataSource extends DataSource<any> {
  _filterChange = new BehaviorSubject("");

  public get filter(): string {
    return this._filterChange.value;
  }
  public set filter(filter: string) {
    this._filterChange.next(filter);
  }
  public data: any[] = [];
  constructor(
    private restService: RestService,
    private searchObject: any,
    private sort: MdSort,
    private paginator: MdPaginator
  ) {
    // constructor(private restService: RestService, private searchObj: any) {
    super();
    this.restService.searchCustomers(this.searchObject).subscribe(results => {
      if (results) {
        this.data = results;
      }
    });
  }

  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): any {
    const displayDataChanges = [
      this.restService.searchCustomers(this.searchObject),
      this._filterChange,
      this.sort.mdSortChange,
      this.paginator.page
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      this.data = this.data.slice().filter((item: any) => {
        // return this.data.slice().filter((item: any) => {
        //console.log(item)
        let searchStr: string =
          item.businessName +
          "" +
          item.firstName +
          "" +
          item.lastName +
          "" +
          item.email +
          "" +
          item.mobile;
        //console.log(searchStr);
        return searchStr.toLowerCase().indexOf(this.filter.toLowerCase()) != -1;
      });

      const myData = this.sortData();

      const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
      return myData.splice(startIndex, this.paginator.pageSize);
    });
    // return Promise.resolve([])
  }

  disconnect() {}

  sortData(): any[] {
    const data = this.data.slice();
    if (this.sort.active && this.sort.direction == "") {
      return data;
    }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = "";
      let propertyB: number | string | Date = "";

      switch (this.sort.active) {
        case "businessName":
          [propertyA, propertyB] = [a.businessName, b.businessName];
          break;
        case "firstName":
          [propertyA, propertyB] = [a.firstName, b.firstName];
          break;
        //case 'email': [propertyA, propertyB] = [a.email, b.email]; break;
        case "state":
          [propertyA, propertyB] = [a.address.state, b.address.state];
          break;
        case "mobile":
          [propertyA, propertyB] = [a.mobile, b.mobile];
          break;
        case "balance":
          [propertyA, propertyB] = [
            a.customerPayment.openingBalance,
            b.customerPayment.openingBalance
          ];
          break;
        case "fromDate":
          [propertyA, propertyB] = [a.fromDate, b.fromDate];
          break;
        case "toDate":
          [propertyA, propertyB] = [a.toDate, b.toDate];
          break;
      }

      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (
        (valueA < valueB ? -1 : 1) * (this.sort.direction == "asc" ? 1 : -1)
      );
    });
  }
}



