import {Item} from "./item.entity";
import { Address } from "./address.entity";

export class Quotations{
  id:string;
  active: boolean = true;
  company: any;
  user: any;
  customer: any;
  memo: string;
  status:any ="DRAFTSAVE";
  isCgst : boolean = true;
  isIgst: boolean = false;
  isProduct : boolean = true;
  isService: boolean = false;
  print: any;
  isBillingAddress : boolean = true;
  discount : number = 0;
  discountType : string;
  cess : number = 0;
  cessValue: number;
  discountValue: any;   
  subTotal:number = 0;
  total:number= 0;
  saveType :string = "save";
  paymentTerms: any;
  address: Address = new Address();
  items: Item[] = [];
  description ="";
  
    estimationDate: Date;
   expirationDate: Date;
   time: Date;
 // dueDate : Date;
  quoteNumber : any;
   quotationMessage : string;
   productItems: Item[] = [];
   serviceItems: Item[] = [];
 
    


    
}


