import { Component, OnInit, Input, ViewContainerRef } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "../../../common/rest.service";
import { Prop } from "../../../common/props";
import { Util } from "../../../shared/utils/util";

import { InventoryFormGroup } from "../../../forms/Product.form";

import { Inventory } from "../../../entities/Product.entity";
import { ProductPurchasing } from "../../../entities/ProductPurchasing.entity";
import { ProductPriceListItems } from "../../../entities/ProductPriceListItems.entity";

@Component({
  selector: "app-inventory",
  templateUrl: "./inventory.edit.component.html"
})
export class InventoryEditComponent {
  myForm: any = InventoryFormGroup.init();
  inventory: Inventory = new Inventory();
  @Input() id;
  purchaseTaxList: any[] = [];
  taxList: any[] = [];
  expenseAccountList: any[] = [];
  revenueAccountList: any[] = [];
  invAssetAccountList: any[] = [];
  uqcList: any[] = [];
  filteredUqcList: any[] = [];
  public isEditable: boolean = true;

  constructor(
    private appService: AppService,
    private restService: RestService,
    vcr: ViewContainerRef
  ) {
    InventoryFormGroup.edit(this.myForm);
    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrieve(id);
    }
    // this.toastr.setRootViewContainerRef(vcr);
  }
  ngOnInit() {
    this.init();
  }
  showWarning() {
    // this.toastr.warning('Cost prize should be less than Selling prize.', 'Alert!',{positionClass: "toast-bottom-center"});
  }

  comparePrize() {
    let costPrize = parseInt(this.inventory.productPriceListItems.unitPrice);
    let sellingPrize = this.inventory.productPriceListItems.unitSellPrice;
    if (costPrize > sellingPrize) {
      this.showWarning();
    }
  }

  private init() {
    this.inventory.productType = "Inventory";
    // let id = this.id;
    // console.log(id);
    // if (this.id) {
    //     this.retrieve(id);
    // }
    this.purchaseTaxLoad();
    this.taxLoad();
    this.uqcLoad();
    this.expenseAccountLoad();
    this.revenueAccountLoad();
    this.invAssetAccountLoad();
  }

  private purchaseTaxLoad() {
    this.restService.dataload("NewPurchaseTax").subscribe(results => {
      if (results) {
        this.purchaseTaxList = results;
      }
    });
  }

  private taxLoad() {
    this.restService.dataload("NewTax").subscribe(results => {
      if (results) {
        this.taxList = results;
      }
    });
  }

  private uqcLoad() {
    //this.uqcList = [{code:'BAG',desc:'BAG'},{code:'BGS',desc:'BAGS'}];
    this.restService
      .dataloadFromFile("./assets/json/unit_quantity_codes.json")
      .subscribe(results => {
        if (results) {
          this.uqcList = results["unitQuantityCodes"];
        }
      });
  }

  filterUQCList(val: string) {
    if (val) {
      this.inventory.uqc = val;
    }
    this.filteredUqcList =
      val && val != ""
        ? this.uqcList.filter(
            s => s.description.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.uqcList;
  }

  private expenseAccountLoad() {
    this.restService.dataload("/expenseAccount").subscribe(results => {
      if (results) {
        this.expenseAccountList = results;
      }
    });
  }

  private revenueAccountLoad() {
    this.restService.dataload("/revenueAccount").subscribe(results => {
      if (results) {
        this.revenueAccountList = results;
      }
    });
  }

  private invAssetAccountLoad() {
    this.restService.dataload("/AssetAccount").subscribe(results => {
      if (results) {
        this.invAssetAccountList = results;
      }
    });
  }

  retrieve(id: string) {
    this.restService.inventoryEntity(id).subscribe(result => {
      if (result) {
        this.inventory = result;
      }
    });
  }

  valid() {
    return true;
  }

  save() {
    if (!this.valid()) return;
    this.inventory.productType = "Inventory";
    this.inventory.companyId = this.appService.getBranch().id;
    this.inventory.active = true;
    this.restService.inventorySave(this.inventory).subscribe(result => {
      if (result) {
        this.inventory = result;
        this.appService.showMessage("Saved successfully.");
        this.myForm.reset();
        //this.appService.navigate('admin/product-search', []);
      }
    });
  }

  add() {
    this.inventory = new Inventory();
    this.myForm = InventoryFormGroup.init();
  }

  back() {
    this.appService.navigate("", []);
  }
}



