import { Component, OnInit, Input, Output, EventEmitter } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { Storage } from "../../../shared/utils/storage";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { ExpenseFormGroup } from "../../../forms/expense.form";
import { ExpenseManagement } from "../../../entities/expensemanagement";
import { SupplierManagement } from "../../../entities/suppliermanagement";

import { ExpenseAccountDetails } from "../../../entities/expenseaccountdetails.entity";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/startWith";
import "rxjs/add/operator/map";
import { FormControl } from "@angular/forms";

@Component({
  selector: "app-expensemanagement",
  templateUrl: "./expensemanagement.view.component.html",
  styleUrls: ["./expensemanagement.component.scss"]
})
export class ExpensemanagementViewComponent implements OnInit {
  myForm: any = ExpenseFormGroup.init();
  expense: any;
  supplierManagement: SupplierManagement;
  firstName: string = null;
  middleName: string = null;
  lastName: string = null;
  suppliers: SupplierManagement[] = [];
  expenseManagement: ExpenseManagement;
  showElectric = false;
  showCheque = false;
  isRecordFound = false;
  isSupplierToggle = true;
  chartAccounts: any[] = [];
  filteredChartAccounts: any[] = [];

  constructor(
    private appService: AppService,
    private restService: RestService
  ) {
    ExpenseFormGroup.edit(this.myForm);
    ExpenseFormGroup.supplierName(this.myForm);

    this.expenseManagement = new ExpenseManagement();

    this.retriveSuppliers();
    // this.chartAccountsLoad();
    this.expense = this.newExpense;

    let id = this.appService.getParam("id");
    if (id !== undefined) {
      this.expenseDetails(id);
      console.log("id :" + id);
    }
  }

  //    onExpenceChange($event) {
  //        if ($event.checked) {
  //            ExpenseFormGroup.supplierName(this.myForm);
  //            this.isSupplierToggle = true;
  //            this.expenseManagement.isSupplier = true;
  //            this.expenseManagement.expenseName = null;
  //
  //        } else {
  //            ExpenseFormGroup.expenseName(this.myForm);
  //            this.isSupplierToggle = false;
  //            this.expenseManagement.isSupplier = false;
  //            this.expenseManagement.supplierId.id = null;
  //
  //        }
  //
  //    }

  expenseDetails(id) {
    this.restService.expenseEdit(id).subscribe(result => {
      if (result) {
        this.expenseManagement = result;
        this.isRecordFound = true;
        this.onPayModeChange(this.expenseManagement.paymentMode);
        this.isSupplierToggle = this.expenseManagement.isSupplier;

        if (this.expenseManagement.isSupplier) {
          ExpenseFormGroup.supplierName(this.myForm);
          this.isSupplierToggle = true;
          this.expenseManagement.isSupplier = true;
          this.expenseManagement.expenseName = null;
        } else {
          ExpenseFormGroup.newPayee(this.myForm);
        }
        this.totalCalculate();
      }
    });
  }

  totalCalculate() {
    let subTotal = 0;
    let totalTax = 0;
    let grandTotal = 0;
    this.expenseManagement.expenseAccountDetails.forEach(item => {
      let itemTax = 0;
      let itemTotal = 0;

      subTotal += item.amount;
      totalTax += item.tax;
    });

    this.expenseManagement.subTotal = parseFloat(subTotal.toString());
    this.expenseManagement.totalTax = parseFloat(totalTax.toString());
    this.expenseManagement.grandTotal =
      this.expenseManagement.subTotal + this.expenseManagement.totalTax;
    this.expenseManagement.amount = this.expenseManagement.grandTotal;
  }

  ngOnInit() {
    //this.supplierLoad();
    // this.payModeLoad();
    this.addItem();
    // this.taxLoad();
    // this.expenseTypeLoad();
  }

  // private chartAccountsLoad(){
  //     this.restService.dataload("chartaccounts")
  //     .subscribe(results =>{
  //         this.chartAccounts = results
  //     });
  // }

  addItem() {
    console.log("inside addItem");
    let item = new ExpenseAccountDetails();
    item.id = null;

    item.expenseTypes.id = null;
    item.description = item.description;
    item.amount = item.amount;
    item.tax = item.tax;
    this.expenseManagement.expenseAccountDetails.push(item);
  }

  newExpense() {
    return {};
  }
  //mycode

  onPayModeChange(val: string) {
    if (val == "Cheque") {
      ExpenseFormGroup.cheque(this.myForm);

      this.showCheque = true;
      this.showElectric = false;
      this.expenseManagement.referenceNo = null;
    } else if (val == "Electronic") {
      ExpenseFormGroup.electric(this.myForm);

      this.showElectric = true;
      this.showCheque = false;
      this.expenseManagement.bankName = null;
      this.expenseManagement.chequeNo = null;
      this.expenseManagement.chequeDate = null;
    } else {
      ExpenseFormGroup.cash(this.myForm);
      this.showCheque = false;
      this.showElectric = false;
      this.expenseManagement.bankName = null;
      this.expenseManagement.chequeNo = null;
      this.expenseManagement.chequeDate = null;
      this.expenseManagement.referenceNo = null;
    }
  }

  finish() {
    this.appService.navigate("./admin/expense-management", []);
  }

  retriveSuppliers() {
    this.restService.retriveSuppliers().subscribe(result => {
      if (result) {
        this.suppliers = result;
        this.suppliers.forEach(item => {
          item.displayName =
            item.firstName + " " + item.middleName + " " + item.lastName;
        });
      }
    });
  }

  filteredSuppliers: any[] = [];

  filterSuppliers(val: string) {
    this.filteredSuppliers =
      val && val != ""
        ? this.suppliers.filter(
            s => s.firstName.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.suppliers;
  }

  //   payModeList: any[] = [];
  //   payModeLoad() {
  //     this.restService.dataload("PayMode").subscribe(results => {
  //       if (results) {
  //         this.payModeList = results;
  //       }
  //     });
  //   }

  //   taxList: any[] = [];
  //   taxLoad() {
  //     this.restService.dataload("Tax").subscribe(results => {
  //       if (results) {
  //         this.taxList = results;
  //       }
  //     });
  //   }
  //   expenseTypeList: any[] = [];
  //   expenseTypeLoad() {
  //     this.restService.dataload("ExpenseType").subscribe(results => {
  //       if (results) {
  //         this.expenseTypeList = results;
  //       }
  //     });
  //   }

  removeItem(index: number) {
    if (index !== -1) {
      // this.sales.items.splice(index, 1);
      // this.expense.expenseDetails.splice(index, 1);
      this.expenseManagement.expenseAccountDetails.splice(index, 1);
    }
  }

  printPriview() {
    //    this.restService.invoiceOpen(this.sales.id);
  }

  done() {
    this.appService.navigate("/admin/expense-search", []);
  }
}



