import { Component, OnInit } from "@angular/core";
import { AppService } from "../../../shared/service/app.service";
import { AnimationService } from "../../../shared/service/animation.service";
import { RestService } from "./../../../common/rest.service";
import { Prop } from "./../../../common/props";
import { SupplierManagement } from "../../../entities/suppliermanagement";
import { ApexService } from "../../../shared/service/apex.service";

@Component({
  selector: "app-suppliermanagement",
  templateUrl: "./suppliermanagement.component.html",
  styleUrls: ["./suppliermanagement.component.scss"]
})
export class SuppliermanagementComponent {
  isRecordFound: boolean;
  currentStep: number = 1;
  step1: any;
  step2: any;
  step3: any;
  supplierManagement: SupplierManagement;
  isActive: boolean = false;
  isEditable: string = "true";
  isEdit: boolean = false;
  constructor(
    private appService: AppService,
    private restService: RestService,
    private apexService: ApexService
  ) {
    this.supplierManagement = new SupplierManagement();
    this.supplierManagement.company.id = this.appService.getBranch().id;
    // this.customerCreation({});
    let id = this.appService.getParam("id");
    if (id !== undefined) {
      this.supplierDetails(id);
    }
  }

  ngOnInit() {
    let id = this.appService.getParam("id");
    this.isEditable = this.appService.getParam("isEditable");
    if (id !== undefined) {
      this.retrieveSupplier(id);
      this.isEdit = true;
    } else {
      this.isEdit = false;
      this.isEditable = "true";
    }
  }

  supplierDetails(id) {
    this.restService.supplierEdit(id).subscribe(result => {
      if (result) {
        this.supplierManagement = result;
        this.isRecordFound = true;
      }
    });
  }
  changeEvent(event1: any, event2: any, event3: any) {
    console.log("activeEvent all steps");
    this.step1 = event1;
    this.step2 = event2;
    this.step3 = event3;
    setTimeout(() => {
      if (!this.isActive) {
        this.isActive = true;
        this.changeStep();
      }
    }, 100);
  }
  changeStep() {
    this.step1.disabled = true;
    this.step2.disabled = true;

    this.step3.disabled = true;
    this.step1.active = false;
    this.step2.active = false;

    this.step3.active = false;

    let activeStep: any = this.getActiveStep();

    activeStep.disabled = false;
    activeStep.active = true;
  }
  retrieveSupplier(id: string) {
    this.restService.getSupplier(id).subscribe(result => {
      if (result) {
        this.supplierManagement = result;

        this.isEdit = true;
      }
    });
  }
  getActiveStep() {
    switch (this.currentStep) {
      case 1:
        return this.step1;
      case 2:
        return this.step2;
      case 3:
        return this.step3;
      default:
        return this.step1;
    }
  }
  supplierEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 2;
    this.changeStep();
  }
  supplierBussinessDetail($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 3;
    this.changeStep();
  }

  addressesEvent($event) {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "complete";
    this.currentStep = 4;
    this.changeStep();
  }

  backEvent() {
    let activeStep: any = this.getActiveStep();
    activeStep.state = "none";
    this.currentStep = this.currentStep - 1;
    this.changeStep();
  }
  supplierCreation($event: any) {
    this.restService.supplierSave(this.supplierManagement).subscribe(result => {
      // alert("result SupplierManagement");
      // alert(result);
      let message = "";
      if (this.isRecordFound) {
        message = "supplier details updated successfully.";
      } else {
        message = "supplier details saved successfully.";
      }
      this.apexService.showMessages(message, 1500);

      if (this.isRecordFound) {
        this.appService.navigate("/admin/suppliersearch", []);
      } else {
        //                      this.supplierManagement = Object.assign({}, new SupplierManagement());
        //                  this.supplierManagement=new SupplierManagement();
        this.appService.navigate("/admin/suppliersearch", []);
        //                      this.currentStep =1;
        //                      this.changeStep();
        // this.changeEvent({},{},{});
        //                     this.appService.navigate('/admin/suppliersearch', []);
        //                 this.appService.navigateMy('/admin/supplier-management');
      }
      // this.appService.navigate('/admin/suppliersearch', []);
      //                 }
    });
  }

  //
  //     }
}



