import { UserSettingFormGroup } from "./../../../forms/usersetting.form";
import { AppService } from "app/shared/service/app.service";
import { Component, OnInit } from "@angular/core";
import { RestService } from "app/common/rest.service";
import { UserSettings } from "app/entities/usersettings.entity";
import { Company } from "app/entities/company.entity";
import { Address } from "app/entities/address.entity";
import { UserDetails } from "app/entities/userdetails.entity";
import { BankDetails } from "app/entities/bank.entity";
import { PrintLayout } from "app/entities/printlayout.entity";
import { Storage } from "../../../shared/utils/storage";
import { MdDialog, MdTableModule } from "@angular/material";
import { Staff } from "app/entities/staff.entity";
import { Location } from "@angular/common";
import { Router } from "@angular/router";
@Component({
  selector: "app-settings",
  templateUrl: "./settings.component.html",
  styleUrls: ["./settings.component.scss"]
})
export class SettingsComponent implements OnInit {
  userSettingForm: any = UserSettingFormGroup.init();
  userSettings: UserSettings = new UserSettings();
  settingControlStatus: any = {};
  layoutSelected: string = "";
  stateList: any[] = [];
  filteredLayout : any[] = [];
  pageLayoutList: any= [{'id':1, 'layout':'A4 SIZE'},
                        {'id':2, 'layout':'80MM SIZE'},
                        {'id':3, 'layout':'A6 SIZE'},
                        {'id':4,'layout':'60MM SIZE'},
                        {'id':5, 'layout':'HALF A4 SIZE'}];
  selectedIndex: number = 0;

  staff: Staff = new Staff();
  isEditable: string = "true";
  isEdit: boolean = false;
  displayedColumns: any[] = [
    "firstName",
    "lastName",
    "mobileNo",
    "status",
    "loggedIn"
  ];
  documentTypes: any[] = [
    "Customers",
    "Stock Item",
    "Purchase Orders",
    "Purchase Invoices",
    "Invoices",
    "Credit or Debit Note",
    "Expanses",
    "Payment Received",
    "Suppliers",
    "Barcode Generation",
    "SUPER USER"
  ];
  dataSource: any[] = [];
  data: any;
  constructor(
    private appService: AppService,
    private restService: RestService,
    private dialog: MdDialog,
    private dialogRef: MdDialog,
    private location: Location
  ) {}
  ngOnInit() {
    UserSettingFormGroup.edit(this.userSettingForm);
    this.retrieveStates();
    //this.retrievePageLayout();
    this.retriveUserSettings();
    Storage.setLocalItem("pageLayoutId", this.userSettings.printLayoutId.id);
    this.staff = new Staff();
  }
  // ngOnInit() {
  //   this.selectedIndex = 0;
  //   UserSettingFormGroup.edit(this.userSettingForm);
  //   this.retrieveStates();
  //   this.retrievePageLayout();
    
  //   let id = this.appService.getParam("id");
  //   this.isEditable = this.appService.getParam("isEditable");
  //   Storage.setLocalItem("pageLayoutId", this.userSettings.printLayoutId.id);
  //   this.retrieveStaffList();
  //   this.staff = new Staff();
  //   if (id !== undefined) {
  //     this.retriveUserSettings(id);
  //   } else {
  //     this.isEdit = false;
  //     this.isEditable = "true";
  //   }
  // }

  retrieveStaffList() {
    this.restService.fetchStaff().subscribe(results => {
      console.log(results);
      this.dataSource = results;
      // console.log(this.dataSource);
    });
    // console.log("sstaff list is s", this.dataSource);
  }
  // edit(template, item: any): void {
  //   if (!item) {
  //     this.staff = new Staff();
  //     this.staff = item;
  //   } else {
  //   this.staff = item;
  //   }
  //   this.dialog.open(template);
  // }
  add(template) {
    setTimeout(() => {
      this.dialog.open(template, {
        width: "600px"
      });
    }, 10);
  }
  myFunction() {
    this.dialogRef.closeAll();
  }
  save() {
    this.restService.saveStaff(this.staff).subscribe(results => {
      this.staff = results;
      this.appService.showMessage("Saved successfully.");
      this.dialogRef.closeAll();
      this.ngOnInit();
      this.userSettingForm.reset();
    });
    // this.dialogRef.closeAll();
  }
  retriveUserSettings() {
    this.restService.retriveUserSettings().subscribe(result => {
      if (result) {
        console.log(result);
        this.userSettings = result;
        
        // this.setDefaults();
        this.isEdit = true;
      }
    });
  }

  retrieveStates() {
    this.restService
      .dataloadFromFile("./assets/json/common.json")
      .subscribe(results => {
        this.stateList = results["stateList"];
      });
  }
  retrievePageLayout() {
    this.restService.dataload("pagelayout").subscribe(results => {
      console.log(results);
      this.pageLayoutList = results;
    });
  }

  filterStates(val: string) {
    this.stateList = Storage.filterStates(this.stateList, val);
  }
  
  filterLayouts(val: string) {
    console.log(val);
    this.filteredLayout =
      val && val != ""
        ? this.pageLayoutList.filter(
            s => s.layout.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : this.pageLayoutList;
  }
  setDefaults() {
    if (!this.userSettings) this.userSettings = new UserSettings();
    if (!this.userSettings.company) {
      this.userSettings.company = new Company();
      this.userSettings.company.id = this.appService.getBranch().id;
      this.userSettings.company.name = this.appService.getBranch().name;
      this.userSettings.companyId = this.appService.getBranch().id;
    }
    if (!this.userSettings.company.address)
      this.userSettings.company.address = new Address();
    if (!this.userSettings.userProfile)
      this.userSettings.userProfile = new UserDetails();
    if (!this.userSettings.bankDetails)
      this.userSettings.bankDetails = new BankDetails();
    if (!this.userSettings.printLayoutId) {
      this.userSettings.printLayoutId = new PrintLayout();
      // TODO: In future remove this and make layout as dropdown
      // this.userSettings.printLayoutId.layoutSize = "A4";
    }
  }

  editClick(formControl: HTMLInputElement) {
    formControl.readOnly = false;
  }
  setPrintLayout(printlayout: any) {
    console.log(printlayout);
    if (this.userSettings.printLayoutId == null) {
      this.userSettings.printLayoutId = new PrintLayout();
    }
    this.userSettings.printLayoutId.layoutSize = printlayout.layout;
    this.userSettings.printLayoutId = printlayout.id;
  }
  saveInfo() {
    console.log("Request:" + JSON.stringify(this.userSettings));
    this.restService.settingsSave(this.userSettings).subscribe(result => {
      if (result) {
        this.userSettings = result;
        this.selectedIndex = 0;
        this.appService.showMessage("Saved successfully.");
      }
    });
  }

  list(template) {
    setTimeout(() => {
      this.dialog.open(template);
    }, 30);
  }
}



