import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { DataSource} from '@angular/cdk';
import { AppService } from '../../../shared/service/app.service';
import { AnimationService } from '../../../shared/service/animation.service';
import { RestService } from '../../../common/rest.service';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { MdSort, MdPaginator } from '@angular/material';

export class ProductDataSource  extends DataSource<any> {
  private data: any[] = [];

  constructor(private restService: RestService, private searchObj: any, private sort: MdSort, private paginator: MdPaginator) {
    super();
    this.getDataList();
  }

  getDataList() {
    console.log(this.searchObj);
    this.restService.showLoader(true);
    this.restService.searchProducts(this.searchObj).subscribe(results => {
      if (results) {
        this.data = results;
        this.restService.showLoader(false);
        console.log(this.data)
      }
    })
  }
  /** Connect function called by the table to retrieve one stream containing the data to render. */
  connect(): any {
    const displayDataChanges = [
      this.restService.searchProducts(this.searchObj),
      this.sort.mdSortChange,
      this.paginator.page,
    ];

    return Observable.merge(...displayDataChanges).map(() => {
      //return this.getSortedData();
      //const myData = this.data.slice();
      const myData = this.getSortedData();;
      
            // Grab the page's slice of data.
            const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
            return myData.splice(startIndex, this.paginator.pageSize);
    });
    // return Promise.resolve([])
  }

  disconnect() { }

  getSortedData(): any[] {
    const data = this.data.slice();
    if (!this.sort.active || this.sort.direction == '') { return data; }

    return data.sort((a, b) => {
      let propertyA: number | string | Date = '';
      let propertyB: number | string | Date = '';
      switch (this.sort.active) {
        case 'lowstockalert': [propertyA, propertyB] = [a.productPriceListItems.stockAlertQty, b.productPriceListItems.stockAlertQty]; break;
        case 'product': [propertyA, propertyB] = [a.productName, b.productName]; break;
        case 'qty': [propertyA, propertyB] = [a.productPriceListItems.initialStockAtHand, b.productPriceListItems.initialStockAtHand]; break;
        //case 'lowstockalert': [propertyA, propertyB] = [a.productPriceListItems.stockAlertQty, b.productPriceListItems.stockAlertQty]; break;
      }
      let valueA = isNaN(+propertyA) ? propertyA : +propertyA;
      let valueB = isNaN(+propertyB) ? propertyB : +propertyB;

      return (valueA < valueB ? -1 : 1) * (this.sort.direction == 'asc' ? 1 : -1);
    });
  }

  
}


