import { Component, OnInit, ViewChild, SimpleChanges } from "@angular/core";
import { RestService } from "../../../common/rest.service";
import { AppService } from "../../../shared/service/app.service";

import { Storage } from "../../../shared/utils/storage";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { BarcodePrintComponent } from "../barcode-print/barcode-print.component";
import { Barcode } from "../../../entities/barcode.entity";
import { StockItem } from "../../../entities/stockitem.entity";
@Component({
  selector: "app-barcode-gen",
  templateUrl: "./barcode-gen.component.html",
  styleUrls: ["./barcode-gen.component.scss"]
})
export class BarcodeGenComponent implements OnInit {
  //barcodeFrom: any = BARCODEFormGroup.init();
  index: number = 0;
  supplierList: any[] = [];
  filteredSuppliers: any[] = [];
  supplierSelected: string = "";

  itemSelected: Barcode;
  barcodesList: Barcode[] = [];
  filteredStockItems: any[] = [];

  isEditable: string = "true";
  isEdit: boolean = false;
  selectedItem: any;
  stockItemList: any[] = [];

  constructor(
    private appService: AppService,
    public dialog: MdDialog,
    private restService: RestService
  ) {}

  ngOnInit() {
    this.loadStockItems();
    this.add();
  }
  loadStockItems() {
    this.restService
      .getStockItems({ id: this.appService.getBranch().id })
      .subscribe(results => {
        this.stockItemList = results;
      });
  }
  filterStockItems(val: string) {
    if (this.isEditable == "true") {
      this.filteredStockItems =
        val && val != ""
          ? this.stockItemList.filter(
              s => s.productName.toLowerCase().indexOf(val.toLowerCase()) >= 0
            )
          : this.stockItemList;
    } else {
      this.filteredStockItems = [];
    }
  }

  selectLineItem(lineItem, index) {
    if (this.itemSelected !== lineItem) {
      this.itemSelected = lineItem;
    }
  }

  selectStockItem(item: StockItem) {
    console.log(item);
    this.itemSelected.stockName = item.productName;
    // TODO this.itemSelected.barcode = item.barcode;
    this.itemSelected.barcode = item.barcode;
    // let isAvailable = false;
    // this.stockItemList.forEach(element => {
    //   if (element.stockName == this.itemSelected.stockName) {
    //     isAvailable = true;
    //   }
    // });
    // if (!isAvailable) this.add();
  }

  itemValueChange(item: any) {
    let numberArray = Array(parseInt(item.qty)).fill(1);
    console.log("numberArray", numberArray);
    this.itemSelected.qtyArray = numberArray;
    this.add();
  }

  add() {
    if (
      this.barcodesList.length == 0 ||
      (this.barcodesList[this.barcodesList.length - 1].stockName != null &&
        this.barcodesList[this.barcodesList.length - 1].stockName != " ")
    ) {
      let item = new Barcode();
      item.qty = 0;
      item.stockName = " ";
      this.barcodesList.push(item);
    }
  }
  removeItem(index) {
    this.barcodesList[index].stockName = null;
    if (this.barcodesList.length > 0) {
      setTimeout(() => {
        this.barcodesList.splice(index, 1);
      }, 10);
    }
  }
  generate() {
    //this.appService.setLocalItem("barcodes", this.barcodesList);
    // this.appService.navigate("admin/barcode-print", []);
  }
  pop() {
    console.log("pop method barcode list is ", this.barcodesList);
    Storage.setLocalItem("barcodes", this.barcodesList);
    setTimeout(() => {
      this.openDialog(null);
    }, 10);
  }
  openDialog(id) {
    const dialogRef = this.dialog.open(BarcodePrintComponent, {
      width: "1200px",
      height: "850px",
      data: id
    });
  }
  reset() {
    this.barcodesList = [];
    setTimeout(() => {
      this.add();
    }, 10);
  }
}



