import {
  Component,
  Inject,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef
} from "@angular/core";
import { MdDialog, MdDialogRef, MD_DIALOG_DATA } from "@angular/material";
import { RestService } from "./../../../../common/rest.service";
import { AppService } from "../../../../shared/service/app.service";

import {
  TdDataTableService,
  TdDataTableSortingOrder,
  ITdDataTableSortChangeEvent,
  ITdDataTableColumn
} from "@covalent/core";
@Component({
  selector: "crdn-delete-dialog",
  template: `
    <h2 style="color:#000;">Delete Confirmation</h2>
    <hr style="background:#fff;" />
    <br />
    <p style="font-size:16px;">
      Are you sure you want to delete?
    </p>
    <br /><br />
    <button
      md-button
      color="primary"
      (click)="ok()"
      style="background:#ffc107; color:#fff;"
    >
      Yes
    </button>
    <button
      md-button
      color="primary"
      (click)="cancel()"
      style="float:right; background:#ffc107; color:#fff;"
    >
      No
    </button>
  `
})
export class CRDNDeleteDialog {
  myData: any;
  constructor(
    private restService: RestService,
    private appService: AppService,
    public dialogRef: MdDialogRef<CRDNDeleteDialog>,
    @Inject(MD_DIALOG_DATA) public data: any
  ) {
    this.myData = data;
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
  //   ok() {
  //     this.restService.crdnDelete(this.myData).subscribe(result => {
  //       this.dialogRef.close();
  //     });
  //   }
  ok() {
    this.restService.crdnDelete(this.myData).subscribe(result => {
      this.dialogRef.close();
      this.appService.showMessage("CRDN Management Deleted Successfully ");
    });
  }
  cancel() {
    this.dialogRef.close();
  }
}



